<template>
  <div class="formula-show" v-loading="loading">
    <el-row>
      <el-col :span="8" v-for="item in list" :key="item.id" class="formula-picture mb">
        <div class="produce-img mb" @click="go(item.id)">
          <img :src="item.bigImage" alt="">
        </div>
        <div class="product-info">
          <p class="product-detail" @click="go(item.id)">
            <router-link :to="{name: 'home'}">{{item.tradeName}}</router-link>
          </p>
          <p class="product-price color1" @click="go(item.id)">
            <span>{{item.price}} 元</span>
          </p>
        </div>
      </el-col>
    </el-row>
    <el-pagination
      background=""
      layout="prev, pager, next"
      :pager-count="5"
      :page-size="6"
      :total="count"
      @current-change="handleCurrentChange"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "MaterialShow",
  props: ["type"],
  data() {
    return {
      tradeType: "美蛙鱼头底料",
      list: [],
      loading: true,
      currentPage: 1,
      count: 0,
      pageSize: 6
    };
  },
  created() {
    this.getData();
  },
  methods: {
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.getData();
    },
    getData() {
      this.loading = true;
      this.$axios
        .get("/baseTrade/list", {
          params: {
            tradeType: this.tradeType,
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          this.count = res.data.count;
          if (res.data.data.length == 0) {
            this.list = [];
            this.loading = false;
          }
          if (res.data.count > 0) {
            this.list = res.data.data;
            this.loading = false;
          }
        });
    },
    go(id) {
      this.$router.push("/material/materialdetail/" + id);
    }
  },
  watch: {
    type(val) {
      this.tradeType = val;
      this.getData(this.tradeType);
    }
  }
};
</script>

<style lang="scss" scoped>
.formula-show {
  width: 100%;
  min-height: 400px;
  position: relative;
  /deep/ .el-row {
    width: 100%;
    margin-top: 40px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    .formula-picture {
      .produce-img {
        width: 300px;
        height: 300px;
        cursor: pointer;
        img {
          width: 100%;
          height: 100%;
          transition: all 0.4s linear;
          box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5);
        }
        img:hover {
          transform: scale(0.95) !important;
        }
      }
      .product-detail a {
        font-size: 16px;
        color: #373737;
        line-height: 25px;
      }
      .product-price {
        display: flex;
        justify-content: space-between;
        span {
          font-size: 14px;
          line-height: 30px;
        }
      }
    }
  }
  .el-pagination {
    position: absolute;
    bottom: 6px;
    left: 408px;
  }
  /deep/.el-pagination.is-background .el-pager li:not(.disabled).active {
    background-color: #bf0000;
  }
}
</style>