import Home from '@/views/Home'
import AppNav from '@/components/AppNav'
import AppFooter from '@/components/AppFooter'
import News from '@/views/News'
import Propaganda from '@/views/Propaganda'
import Teaching from '@/views/Teaching'
import FormulaTrading from '@/views/FormulaTrading'
import MaterialMall from '@/views/MaterialMall'
import Cart from '@/views/Cart'
import Mywise from '@/views/Mywise'
import MineNav from '@/components/MineNav'
import MineCheck from '@/views/MineCheck'
import MineMessage from '@/views/MineMessage'
import MineOrder from '@/views/MineOrder'
import FormulaOrder from '@/views/FormulaOrder'
import MineShopcar from '@/views/MineShopcar'
import UploadFormula from '@/views/UploadFormula'
import PersonalDate from '@/views/PersonalDate'
import test from '@/components/test.vue'
import Detail from '@/views/Detail'
import MenuDetail from '@/views/MenuDetail'
import MaterialDetail from '@/views/MaterialDetail'
import PayMent from '@/views/PayMent'
import Sell from '@/components/Sell'
import InformationDetail from '@/views/InformationDetail'

export default [{
    path: '/formulatrading/detail/:id',
    name: 'detail',
    components: {
      default: Detail,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  },
  {
    path: '/propaganda/menudetail/:id',
    name: 'menudetail',
    components: {
      default: MenuDetail,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  },
  {
    path: '/material/Materialdetail/:id',
    name: 'materialdetail',
    components: {
      default: MaterialDetail,
      aside: AppNav,
      footer: AppFooter

    },
    props: {
      aside: false
    },
    meta: {
      isNav: false
    }
  },

  {
    path: '/payment',
    name: 'payment',
    components: {
      default: PayMent,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  },

  {
    path: '/MineCheck',
    components: {
      default: MineCheck,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }

  },
  {
    path: '/MineMessage',
    components: {
      default: MineMessage,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }

  },
  {
    path: '/MineOrder',
    components: {
      default: MineOrder,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }

  },
  {
    path: '/FormulaOrder',
    components: {
      default: FormulaOrder,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }

  },
  {
    path: '/UploadFormula',
    components: {
      default: UploadFormula,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  },
  {
    path: '/MineShopcar',
    components: {
      default: MineShopcar,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }

  },
  {
    path: '/test',
    components: {
      default: test,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false,
      title: "我的川料"
    }
  },
  {
    path: '/',
    redirect: '/home',
    meta: {
      isNav: false
    }
  },
  {
    path: '/Mywise',
    name: 'Mywise',
    components: {
      default: Mywise,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false,
      title: "我的川料"
    }
  },
  {
    path: '/home',
    name: 'home',
    components: {
      default: Home,
      aside: AppNav,
      footer: AppFooter,

    },
    meta: {
      isNav: true,
      title: '首页'
    }
  },
  {
    path: '/news',
    name: 'news',
    components: {
      default: News,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: true,
      title: '新闻资讯'
    }
  },
  {
    path: '/news/informationDetail/:id',
    name: 'informationDetail',
    components: {
      default: InformationDetail,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  },

  {
    path: '/propaganda',
    name: 'propaganda',
    components: {
      default: Propaganda,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: true,
      title: '宣传'
    },
  },
  {
    path: '/teaching',
    name: 'teaching',
    components: {
      default: Teaching,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: true,
      title: '教学视频'
    }
  },
  {
    path: '/formulatrading',
    name: 'formulatrading',
    components: {
      default: FormulaTrading,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: true,
      title: '配方交易'
    }
  },
  {
    path: '/formulatrading/sell',
    name: 'sell',
    components: {
      default: Sell,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  },
  {
    path: '/material',
    name: 'materialmall',
    components: {
      default: MaterialMall,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: true,
      title: '底料商城'
    },
  },
  {
    path: '/PersonalDate',
    components: {
      default: PersonalDate,
      aside: MineNav,
      footer: AppFooter
    },
    meta: {
      isNav: false,
    }

  },
  {
    path: '/cart',
    name: 'cart',
    components: {
      default: Cart,
      aside: AppNav,
      footer: AppFooter
    },
    meta: {
      isNav: false
    }
  }
]
