<template>
  <div class="FormulaOrder" v-loading="loading">
    <div class="title">
      <h1>我的川料</h1>
    </div>
    <div class="gray_1" v-show="contentflag">
      <div v-for="(item,index) in gray" :key="index">{{item}}</div>
    </div>
    <!-- 购物车部分 -->
    <div class="shop_detail" v-for="(item,index) in formulaList" :key="index" v-show="contentflag">
      <div class="gray_2">
        <span></span>
        <span>配方编号：{{item.id}}</span>
      </div>
      <div class="shop_one">
        <div class="img_box">
          <img :src="item.formula_img" alt="" width="100%" height="100%">
        </div>
        <span>{{item.formula_name}}</span>
        <span class="span_2">x1</span>
      </div>
      <div class="shop_two">
        <span>￥{{item.formula_price}}</span>
      </div>
      <div class="shop_three">
        <span>{{item.create_time}}</span>
      </div>
      <div class="shop_four">
        <a :href="item.formula_file">
          <el-button type="success" size="mini" class="pay">点击下载配方</el-button>
        </a>
        <a>
          <el-button type="danger" size="mini" class="remove" @click="removeFormula(index)">删除配方记录</el-button>
        </a>
      </div>
    </div>
    <p class="contentflag" v-show="!contentflag">暂时还没有可查看的订单哟~</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      curIndex: 0,
      gray: ["订单详情", "实付款", "创建时间", "下载配方"],
      accountInformation: {},
      formulaList: [],
      flag: "true",
      status: "",
      loading: true,
      contentflag: true

    };
  },
  created() {
    this.findFormulaList();
    this.getAccountInformation();
    this.getStatus();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    //获取用户信息
    getAccountInformation() {
      this.loading = true;
      let userString = sessionStorage.getItem("user");
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/user/queryUserByPhone?", {
          params: {
            phone: this.user_id
          }
        })
        .then(res => {
          this.accountInformation = res.data.data;
          this.loading = false;
        });
    },
    //查询已购买所有配方
    findFormulaList() {
      this.loading = true;
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .get("/formula/queryUserFormula", {
          params: {
            user_phone: userPhone
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            this.formulaList = res.data.data;
            this.loading = false;
            if (this.formulaList.length == 0) {
              this.contentflag = false;
            }
          }
        });
    },
    //获取购买状态
    getStatus() {
      if (this.flag == true) {
        this.status = "未下载";
      } else {
        this.status = "已下载";
      }
    },
    //删除配方记录
    removeFormula(index) {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("/formula/removeUserFormula", {
          user_phone: userPhone,
          formula_id: this.formulaList[index].formula_id
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            //回调函数里面删除表格数据
            this.formulaList.splice(index, 1);
          }
        });
    }
  },
  //监听数组的变化
  watch: {
    formulaList(val) {
      console.log(val);
      if (val.length == 0) {
        this.contentflag = false;
      }
    }
  }
};
</script>

