<template>
  <div class="wrapped" v-loading="loading">
    <ul class="menu">
      <!-- 如果路由参数等于当前item 则改变颜色 -->
      <li
        v-for="(item,index) in menus"
        :key="index"
        ref="item"
        :class="type==item?'active':''"
        @click="go(item)"
      >{{item}}</li>
    </ul>
    <div class="menudetail">
      <div class="zoom">
        <div class="smallImg">
          <img :src="menudetail.image" alt="商品展示">
          <span class="zoomspan"></span>
          <div class="decs">
            <h1>{{menudetail.templateName}}</h1>
            <p class="text">
              描述:
              <span>{{menudetail.textDescription}}</span>
            </p>
            <p class="text">
              分类:
              <span
                v-for="(item,index) in menudetail.sort"
                :key="index"
                @click="select(index)"
                :class="{'border':index===sortIndex}"
              >{{item}}</span>
            </p>
            <p>
              价格:
              <span class="price">{{ menudetail.price}}</span>
            </p>
            <p>
              <button @click="addToCart()">加入购物车</button>
              <!-- <button class="green">一键购</button> -->
            </p>
          </div>
        </div>
        <div class="bigImg">
          <img :src="menudetail.image" alt="商品展示" class="pic">
        </div>
      </div>
    </div>
    <ul class="btn">
      <li
        v-for="(item,index) in title"
        :key="index"
        @click="change(index)"
        :class="{'tab':index===curIndex}"
      >{{item}}</li>
    </ul>
    <div class="imgs" v-show="!show">
      <img :src="menudetail.image" alt="">
      <img :src="menudetail.image" alt="">
      <img :src="menudetail.image" alt="">
    </div>
    <div v-show="show">
      <textarea class="critical" ref="submitContent" placeholder="请输入您的评论" @blur="getSubmitContent"></textarea>
      <button class="submit" @click="submitReview()">提交</button>
      <div class="allMessage" style="width:99%" :class="{'show':this.count == 0}">
        <h3>评论列表</h3>
        <div class="message" v-for=" item in this.message " :key="item.id">
          <div>{{item.user_id}}:</div>
          <div class="userContent">{{item.content}}</div>
          <div class="createTime">{{item.create_time}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import routes from "@/router/routes";
export default {
  data() {
    return {
      menus: ["说明", "菜单", "传单", "海报"],
      menudetail: {},
      sort: ["背胶无", "自粘背胶"],
      num: 1,
      checked: false,
      remove: false,
      newDetail: {},
      flag: false,
      type: this.$route.query.type,
      title: ["其他", "评论"],
      curIndex: 0,
      show: false,
      sortIndex: 0,
      loading: true,
      submitContent: "",
      isshow: false,
      message: [],
      count: 0
    };
  },

  created() {
    this.getData();
    this.getMessage();
  },
  mounted() {
    this.zoomPic();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    getData() {
      this.loading = true;
      this.$axios
        .get("/template/list", {
          params: {
            templateType: this.type
          }
        })
        .then(res => {
          if (res.data.count > 0) {
            let array = res.data.data.filter(
              item => item.id === this.$route.params.id
            );
            this.menudetail = array[0];
            this.menudetail["sort"] = this.sort;
            console.log(this.menudetail);
            this.loading = false;
          }
        });
    },
    //提交评论执行请求方法
    submitReview() {
      //获取sess中个人资料
      let userString = sessionStorage.getItem("user");
      //判断是否为登录状态
      if (userString == null) {
        this.tipsOn();
      } else {
        this.user_id = JSON.parse(userString).phone;
        //在登陆状态下提交评论时判断内容是否为空
        if (this.submitContent == "") {
          this.isNull();
        } else {
          //在登录状态下提交评论不为空时允许提交到后台服务器进行审核
          this.$axios
            .post("/leaveMessage/addLeaveMessage/", {
              content: this.submitContent,
              user_id: this.user_id,
              recipient_id: this.menudetail.id
            })
            .then(res => {
              //判断提交是否成功
              if (res.data.msg === "留言出错") {
                this.defeated();
              } else {
                //提交成功后的提示窗口
                this.success();
                //清空评论输入框
                this.$refs.submitContent.value = "";
              }
            });
        }
      }
    },
    //获取评论框文本的方法
    getSubmitContent() {
      this.submitContent = this.$refs.submitContent.value;
      console.log(this.submitContent);
    },
    //提交评论失败后提示框显示方法
    defeated() {
      this.$alert("提交失败", "评论提交", {
        dangerouslyUseHTMLString: true
      });
    },
    //提交评论成功后提示框显示方法
    success() {
      this.$alert("提交成功等待审核", "评论提交", {
        dangerouslyUseHTMLString: true
      });
    },
    //请登陆后在评论
    tipsOn() {
      this.$alert("请登录您的账号再提交", "温馨提示", {
        dangerouslyUseHTMLString: true
      });
    },
    //提交评论为空
    isNull() {
      this.$alert("请填写内容后提交", "温馨提示", {
        dangerouslyUseHTMLString: true
      });
    },
    getMessage() {
      this.$axios
        .get("/leaveMessage/queryLeaveMessageList", {
          params: {
            page: 1,
            limit: 15,
            recipient_id: this.$route.params.id
          }
        })
        .then(res => {
          console.log(res);
          this.message = res.data.data;
          this.count = res.data.count;
        });
    },
    addToCart() {
      let newDetail = {
        ...this.menudetail,
        tradeName: this.menudetail.templateName,
        img: this.menudetail.image,
        num: this.num,
        checked: this.checked,
        remove: this.remove
      };
      this.newDetail = newDetail;
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请先登录再购买",
          type: "warning"
        });
      } else {
        this.interface();
      }
    },
    interface() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("/order/addGoods", {
          user_phone: userPhone,
          goods_price: this.newDetail.price,
          goods_name: this.newDetail.tradeName,
          goods_img: this.newDetail.img,
          goods_count: this.newDetail.num,
          subtotal: this.newDetail.price * this.newDetail.num
        })
        .then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: "恭喜你，商品已经成功添加",
              type: "success"
            });
          }
        });
    },
    change(index) {
      this.curIndex = index;
      if (this.curIndex === 0) {
        this.show = false;
      } else {
        this.show = true;
      }
    },
    select(index) {
      this.sortIndex = index;
    },
    //回到列表页
    go(type) {
      this.$router.push({
        path: "/propaganda",
        query: {
          type
        }
      });
    }
  },
  watch: {
    $route: {
      handler(n) {
        this.type = n.query.type;
        //进入或者退出当前路由时 存在获取不到路由参数
        if (this.type !== undefined) {
          this.getData();
        }
      },
      deep: true
      // immedial: true
    }
  }
};
</script>

<style lang="scss" scoped>
.border {
  border: 1px solid darkgoldenrod;
}
.tab {
  background-color: black;
  color: #ffffff;
}
.active {
  background-color: #bf0000 !important;
  color: #ffffff !important;
  border: none !important;
}
.wrapped {
  width: 1472px;
  height: 825px;
  margin-top: -4px;
  // position: relative;
  .menu {
    width: 100%;
    height: 115px;
    display: flex;
    flex-direction: row;
    li {
      width: 68px;
      height: 36px;
      text-align: center;
      line-height: 36px;
      border: 1px solid #cccccc;
      color: black;
      margin-right: 20px;
      margin-top: 35px;
      font-size: 12px;
      border-radius: 5px;
      cursor: pointer;
      &:hover {
        box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
        font-size: 14px;
      }
    }
  }
  .menudetail {
    width: 96%;
    height: 290px;
    margin-left: 40px;
    margin-top: 0;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .zoom {
      min-width: 1000px;
      height: 400px;
      .smallImg {
        width: 540px;
        height: 290px;
        float: left;
        box-shadow: 12px 12px 12px 0 rgba(0, 0, 0, 0.1);
        /* 定位span元素 也即拖拽元素的父元素 */
        position: relative;
        img {
          width: 100%;
          height: 100%;
        }
        .zoomspan {
          position: absolute;
          width: 95px;
          height: 95px;
          top: 0;
          left: 0;
          background: rgba(200, 200, 200, 0.7);
          cursor: move;
          display: none;
        }
        .decs {
          width: 412px;
          height: 290px;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          padding-left: 5px;
          margin-left: 45px;
          position: absolute;
          top: -2px;
          right: -454px;
          h1 {
            font-size: 30px;
          }
          .text {
            font-size: 20px;
            width: 100%;
            height: 50px;
            line-height: 40px;
            letter-spacing: 2px;
            white-space: nowrap;
            text-overflow: ellipsis;
            overflow: hidden;
            span {
              color: #909399;
            }
          }
          .text:nth-child(3) {
            span {
              display: inline-block;
              width: 110px;
              height: 40px;
              line-height: 40px;
              border-radius: 10px;
              margin-right: 35px;
              margin-left: 20px;
              text-align: center;
              font-size: 12px;
              font-weight: 600;
              background-color: darkgoldenrod;
              color: #ffffff;
              cursor: pointer;
              box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
              &:hover {
                font-size: 14px;
              }
            }
          }
          p:nth-child(4) {
            font-size: 20px;
            .price {
              font-size: 20px;
              font-weight: 600;
              color: #bf0000;
            }
          }

          button {
            width: 123px;
            height: 54px;
            line-height: 54px;
            background-color: #bf0000;
            border-radius: 40px;
            text-align: center;
            color: #ffffff;
            font-weight: 600;
            font-size: 12px;
            margin-right: 37px;
            outline: none;
            cursor: pointer;
          }
          button:nth-child(2) {
            background-color: green;
          }
        }
      }
      .bigImg {
        width: 540px;
        height: 290px;
        float: right;
        overflow: hidden;
        display: none;
        /* 拖拽元素的父元素 */
        position: relative;
        z-index: 1000;
        img {
          width: 200%;
          height: 200%;
          position: absolute;
        }
      }
    }
  }
  .btn {
    width: 100%;
    height: 89px;
    display: flex;
    flex-direction: row;
    border-bottom: 1px solid #cccccc;
    cursor: pointer;
    li {
      width: 68px;
      height: 40px;
      text-align: center;
      line-height: 40px;
      border: 1px solid black;
      margin-right: 20px;
      margin-top: 35px;
      font-size: 12px;
      font-weight: 600;
      &:hover {
        font-size: 14px;
        box-shadow: 0px 0px 8px rgba(0, 0, 0, 0.5);
      }
    }
  }
  .imgs {
    width: 105%;
    height: 207px;
    margin-top: 38px;
    cursor: pointer;
    img {
      width: 320px;
      height: 204px;
      margin: 0 15px;
      box-shadow: 0px 5px 8px rgba(0, 0, 0, 1.5);
      &:hover {
        box-shadow: 0px 5px 8px rgba(0, 0, 0, 1.5);
        transform: translate(0px, -5px);
        transition: all 0.7s;
      }
    }
  }
  textarea {
    width: 94%;
    height: 100%;
    margin-top: 20px;
    outline: #cccccc;
    padding: 30px;
    font-size: 16px;
    resize: none;
  }
  .submit {
    height: 50px;
    width: 14.5%;
    border-radius: 30px;
    outline: none;
    margin-top: 50px;
    margin-left: 85%;
    margin-bottom: 30px;
    background-color: #bf0000;
    color: #eee;
    cursor: pointer;
  }
  .show {
    display: none;
  }
  .allMessage {
    width: 70%;
    h3 {
      padding-bottom: 30px;
    }
    .message {
      padding: 15px 15px 15px 15px;
      border: 1px solid #7f7f7f;
      vertical-align: top;
      .userContent {
        display: block;
        font-size: 14px;
        line-height: 20px;
        padding-top: 8px;
        text-indent: 2em;
      }
      .createTime {
        text-align: right;
        padding-top: 15px;
        font-size: 12px;
        padding-right: 10px;
      }
    }
  }
}
</style>