var zoomPic = function () {
  var smallImg = document.querySelector(".smallImg");
  var bigImg = document.querySelector(".bigImg");
  var span = document.querySelector(".zoomspan");
  var pic = document.querySelector(".pic");
  // 绑定鼠标移入移除事件
  smallImg.onmouseover = function () {
    span.style.display = "block";
    bigImg.style.display = "block";

  };
  smallImg.onmouseout = function () {
    span.style.display = "none";
    bigImg.style.display = "none";

  };
  // 绑定移动事件
  smallImg.onmousemove = function (event) {
    var event = event || window.event;
    span.style.display = "block";
    bigImg.style.display = "block";
    // 移动——获取鼠标的位置
    // console.log(smallImg.offsetLeft)
    var x = event.clientX - smallImg.offsetLeft - span.offsetWidth * 0.5;
    // console.log(event.clientX )
    // console.log(x)
    var y = event.clientY - smallImg.offsetTop;
    // 限定拖拽范围
    if (x <= 0) {
      x = 0;
    };

    if (x >= smallImg.offsetWidth - span.offsetWidth) {
      x = smallImg.offsetWidth - span.offsetWidth;
    };
    if (y <= 0) {
      y = 0;
    };
    if (y >= smallImg.offsetHeight - span.offsetHeight) {
      y = smallImg.offsetHeight - span.offsetHeight
    };

    span.style.left = x + "px";
    span.style.top = y + "px";
    // 计算比例当前位置与可移动范围的比值
    var a = x / (smallImg.offsetWidth - span.offsetWidth);
    var b = y / (smallImg.offsetHeight - span.offsetHeight);

    //求大图的当前位置
    pic.style.left = -a * (pic.offsetWidth - bigImg.offsetWidth) + "px"
    pic.style.top = -b * (pic.offsetHeight - bigImg.offsetHeight) + "px"

  }

}
export {
  zoomPic
}
