<template>
  <div class="common" v-loading="loading">
    <el-row :gutter="24" class="block">
      <!-- 轮播 -->
      <el-col :span="16">
        <div class="banner">
          <el-carousel trigger="click">
            <el-carousel-item v-for="item in list" :key="item.id">
              <h3 class="small">
                <a :href="item.url" target="_blank">
                  <img :src="item.banner_img">
                </a>
              </h3>
            </el-carousel-item>
          </el-carousel>
        </div>
      </el-col>
      <!-- 轮播旁的图片 -->
      <el-col :span="8">
        <div class="grid-content bg-purple">
          <el-row :gutter="20" class="banner-right" v-for="item in smallImg" :key="item.id">
            <el-col :span="11">
              <a :href="item.url" target="_blank" width="100%">
                <img :src="item.smallImage_img" alt="">
              </a>
              <span class="description">{{item.description}}</span>
            </el-col>
          </el-row>
        </div>
      </el-col>
    </el-row>
    <!-- 最新文章 -->
    <div class="news">
      <NewsTemplate/>
    </div>
  </div>
</template>

<script>
import NewsTemplate from "@/components/NewsTemplate";

export default {
  name: "Home",
  components: {
    NewsTemplate
  },
  data() {
    return {
      list: [],
      smallImg: [],
      loading: true
    };
  },
  created() {
    this.getData();
    this.getSmallImg();
  },
  mounted() {},
  methods: {
    getData() {
      this.$axios.get("/home/getBannerShowList").then(res => {
        this.list = res.data.data;
      });
    },
    getSmallImg() {
      this.loading = true;
      this.$axios.get("/home/getSmallImageShowList").then(res => {
        this.smallImg = res.data.data;
        this.loading = false;
      });
    }
  }
};
</script>

<style lang="scss" scoped>

.common {
  width: 1040px;
  margin-top: 15px;
  .block {
    width: 104%;
    height: 300px;
    .banner {
      cursor: pointer;
      box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
    }
    .banner-right {
      width: 56%;
      height: 145px;
      margin-bottom: 10px;
      :hover {
        transition: all 0.5s linear;
        .description {
          position: absolute;
          top: 30px;
          left: 14px;
          font-size: 16px;
          color: #ffffff;
          font-weight: 600;
          transition: all 0.6s;
          transform: translate(0px, -3px);
        }
      }
    }
  }
}

.grid-content {
  height: 145px;
  border-radius: 4px;
  min-height: 36px;
  float: left;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  transition: all 0.6s linear;
  :hover {
    transform: rotateX(30deg);
  }
  img {
    width: 217%;
    height: 89%;
    padding: 2px;
    cursor: pointer;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
  }
  .description {
    position: absolute;
    top: 124px;
    left: 14px;
    font-size: 14px;
    color: #ffffff;
    font-family: "微软雅黑";
  }
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
// 走马灯
.el-carousel__item h3 {
  font-size: 14px;
  opacity: 0.75;
  line-height: 300px;
  margin: 0;
}
.el-carousel__item:nth-child(2n + 1) {
  background-color: #d3dce6;
}
.el-carousel .el-carousel--horizontal {
  overflow-y: hidden;
}
.bg-purple {
  background-color: #ffffff;
}
</style>
