<template>
  <div class="modal-content" @close="go">
    <header>
      <img class="logo" src="@/assets/img/logo.png" alt="">
    </header>
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm modal-body"
    >
      <el-form-item label="" prop="phone" class="input-area">
        <el-input type="text" placeholder="请输入手机号" v-model="ruleForm.phone" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password" class="input-area">
        <el-input
          type="password"
          placeholder="请输入密码（6-16位数组与字母）"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="submit" @click="submitForm('ruleForm')">登录</el-button>
      </el-form-item>
    </el-form>
    <footer>
      还没有账号？
      <span class="change-resgister" @click="goRegister">去注册</span>
    </footer>
  </div>
</template>

<script>
export default {
  name: "Login",
  props: ["reset"],
  data() {
    //验证手机号码
    var phone = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入手机号码"));
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        if (reg.test(value)) {
          //必须有该成功回调
          callback();
        } else {
          return callback(new Error("请输入正确的手机号"));
        }
      }
    };
    //验证密码
    var password = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else {
        const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
        if (reg.test(value)) {
          callback();
        } else {
          return callback(new Error("密码格式错误"));
        }
      }
    };
    return {
      ruleForm: {
        phone: "",
        password: ""
      },
      rules: {
        phone: [{ validator: phone, trigger: "blur" }],
        password: [{ validator: password, trigger: "blur" }]
      }
    };
  },
  methods: {
    //表单提交
    submitForm(ruleForm) {
      this.$refs[ruleForm].validate(valid => {
        if (valid) {
          this.login();
        } else {
          console.log("error submit!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs.ruleForm.resetFields();
    },
    //登录
    login() {
      this.$axios
        .post("/front/login", {
          phone: this.ruleForm.phone,
          password: this.ruleForm.password
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            var user = {
              phone: this.ruleForm.phone,
              password: this.ruleForm.password
            };
            sessionStorage.setItem("user", JSON.stringify(user));
            this.closeDialog();
            this.go();
            this.$emit("getUser");
          } else {
            this.$message({
              message: "用户不存在，请先注册！",
              type: "warning"
            });
          }
        });
    },

    //关闭登录模态框
    closeDialog() {
      this.$emit("closeDialog", false);
    },
    //没有账号去注册
    goRegister() {
      this.$emit("reg", false);
    },
    //跳转登录前路由
    go() {
      this.$router.push(this.$route.path);
    }
  }
};
</script>

<style lang="scss" scoped >
.modal-content {
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
  font-size: 16px;
  header {
    height: 110px;
    .logo {
      margin-left: 198px;
      margin-top: 30px;
      width: 64px;
    }
  }
  .modal-body {
    text-align: center;
    margin: 0 auto;
    margin-top: 10px;
    .el-form-item {
      width: 85%;
      outline: none;
      /deep/.el-form-item__content {
        margin-left: 70px !important;
        /deep/.el-input {
          height: 56px;
          color: #262626;
          font-size: 16px;
          .el-input__inner {
            width: 100%;
            height: 80%;
            border-radius: 0px;
            outline: none;
            border: none;
            border-bottom: 1px solid #dcdfe6;
          }
        }
      }
    }
    .el-form-item:nth-child(3) {
      .el-input {
        width: 65%;
      }
      /deep/.code-send {
        width: 35%;
        height: 56px;
        text-align: center;
        outline: none;
        border: none;
        background-color: #fff;
        &:hover {
          border: none;
          background-color: #fff;
        }
      }
    }
    .submit {
      width: 100%;
      height: 100%;
    }
  }
  footer {
    color: #a7a7a7;
    font-size: 14px;
    padding-bottom: 40px;
    padding-top: 20px;
    text-align: center;
    span {
      color: #4285f4;
      cursor: pointer;
    }
  }
}
</style>
