<template>
  <div class="formulaUpload" v-loading="loading">
    <div class="title">
      <h1>我的川料</h1>
    </div>
    <el-table
      :data="tableData"
      style="width: 100%"
      max-height="750"
      :header-cell-style="{
    'background-color': '#fafafa',
    'color': 'rgb(103, 194, 58)',
    'border-bottom': '1px #000000 solid'
}"
    >
      <el-table-column prop="create_time" label="上传日期"></el-table-column>
      <el-table-column prop="formula_upload_name" label="配方名称"></el-table-column>
      <el-table-column prop="description" label="详细信息"></el-table-column>
      <el-table-column prop="status" label="审核状态"></el-table-column>
      <el-table-column label="操作" width="120">
        <template slot-scope="scope">
          <el-button
            @click.native.prevent="deleteRow(scope.$index, tableData)"
            type="danger"
            size="small"
          >删除</el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  data() {
    return {
      loading: true,
      tableData: []
    };
  },
  created() {
    this.getData();
  },
  methods: {
    //删除数据
    deleteRow(index, rows) {
      this.$axios
        .post("formula/removeFormulaUpload", {
          formula_upload_id: rows[index].id
        })
        .then(res => {
          if (res.data.status == 200) {
            rows.splice(index, 1);
          }
        });
    },
    //获取数据
    getData() {
      this.loading = true;
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .get("/formula/queryFormulaUpload", {
          params: {
            user_phone: userPhone
          }
        })
        .then(res => {
          if (res.status == 200) {
            let arr = res.data.data;
            arr.forEach(item => {
              if (item.status == 0) {
                item["status"] = "待审核";
                this.tableData.push(item);
              }
              if (item.status == 1) {
                item["status"] = "审核通过";
                this.tableData.push(item);
              }
              if (item.status == 2) {
                item["status"] = "未通过";
                this.tableData.push(item);
              }
            });
            this.loading = false;
          }
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.formulaUpload {
  width: 100%;
  .title {
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 40px;
  }
  .el-table {
    margin-top: 46px;
    border-top: 1px solid black;
  }
}
</style>


