<template>
  <div class="formula-show">
    <el-row>
      <el-col :span="7" v-for="item in list" :key="item.id" class="formula-picture mb">
        <div class="produce-img mb" @click="go(item.id)">
          <img :src="item.bigPicture" alt="">
        </div>
        <div class="product-info">
          <p class="product-detail" @click="go(item.id)">
            <router-link :to="{name: 'home'}">{{item.formulaName}}</router-link>
          </p>
          <p class="product-price color1" @click="go(item.id)">
            <span>{{item.formulaPrice}} 元</span>
          </p>
        </div>
      </el-col>
    </el-row>
    <el-row>
      <el-pagination
        background=""
        layout="prev, pager, next"
        :total="count"
        :page-size="6"
        :pager-count="5"
        @current-change="handleCurrentChange"
      ></el-pagination>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "ProductShow",
  data() {
    return {
      value: true,
      list: [],
      currentPage: 1,
      pageSize: 6,
      count: 0
    };
  },
  created() {
    this.getdata();
  },

  methods: {
    go(id) {
      this.$router.push("/formulatrading/detail/" + id);
    },
    //获取当前页数
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.getData();
    },
    getdata() {
      this.$axios
        .get("/formula/list", {
          params: {
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          this.count = res.data.count;
          this.list = res.data.data;
          this.sendLoading();
        });
    },
    sendLoading() {
      this.$emit("load", false);
    },
    changge(val) {
      this.page = `${val}`;
      this.getdata();
    }
  }
};
</script>

<style lang="scss" scoped>
.formula-show {
  width: 105%;
  .formula-picture {
    margin-right: 45px;
    .produce-img {
      height: 300px;
      cursor: pointer;
      img {
        width: 100%;
        height: 100%;
        transition: all 0.4s linear;
        box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5);
      }
      img:hover {
        transform: scale(0.95) !important;
      }
    }
    .product-detail a {
      font-size: 16px;
      color: #373737;
      line-height: 25px;
    }
    .product-price {
      display: flex;
      justify-content: space-between;

      span {
        font-size: 14px;
        line-height: 30px;
      }
    }
  }
  .el-pagination {
    text-align: center;
    margin-top: 50px;
  }
  /deep/.el-pagination.is-background .el-pager li:not(.disabled).active {
    background-color: #bf0000;
  }
}
</style>
