<template>
  <div class="personaldate">
    <div class="title">
      <h1>我的川料</h1>
    </div>
    <div class="btn" @click="submitFromData()">
      <h3>提交</h3>
    </div>
    <div class="top_left">
      <h2 class="to_bottom">姓名</h2>
      <br>
      <input
        type="text"
        placeholder="您的姓名"
        name="name"
        autocomplete="off"
        ref="name"
        @blur="getName"
        :value="this.accountInformation.name"
      >
      <h2 class="h2_dis">您的收货地址</h2>
      <br>
      <input
        type="text"
        placeholder="您的收货地址"
        name="name"
        autocomplete="off"
        ref="address"
        @blur="getAddress"
        :value="this.accountInformation.address"
      >
    </div>
    <div class="top_right">
      <h2 class="to_bottom">邮箱</h2>
      <br>
      <input
        type="text"
        placeholder="您的邮箱地址"
        name="name"
        autocomplete="off"
        ref="email"
        @blur="getEmail"
        :value="this.accountInformation.email"
      >
      <h2 class="to_bottom2">性别</h2>
      <br>
      <select name="" id="" ref="sex" @blur="getSex">
        <option
          :value="this.accountInformation.gender"
        >{{this.accountInformation.gender === 1 ? "女":"男"}}</option>
        <option :value="this.towSex">{{this.accountInformation.gender === 1 ? "男":"女"}}</option>
      </select>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      name: "",
      address: "",
      email: "",
      sex: 1,
      accountInformation: [],
      towSex: ""
    };
  },
  created() {
    this.getAccountInformation();
  },
  methods: {
    //获取名字
    getName() {
      this.name = this.$refs.name.value;
    },
    //获取邮箱
    getEmail() {
      let i = /^([0-9A-Za-z\-_\.]+)@([0-9a-z]+\.[a-z]{2,3}(\.[a-z]{2})?)$/g;
      this.email = this.$refs.email.value;
      if (i.test(this.email)) {
      } else {
        this.prompt("邮箱格式错误", "请输入正确的邮箱");
      }
    },
    //获取电话号码
    getAddress() {
      this.address = this.$refs.address.value;
    },
    //获取性别
    getSex() {
      this.sex = this.$refs.sex.value;
      console.log(this.$refs);
    },
    //提交个人信息到后台保存
    submitFromData() {
      let userString = sessionStorage.getItem("user");
      this.user_id = JSON.parse(userString).phone;
      console.log(this.sex);
      this.$axios
        .post("/user/modifyUserInfo", {
          phone: this.user_id,
          email: this.email,
          name: this.name,
          gender: this.sex,
          address: this.address
        })
        .then(res => {
          console.log(res.data.msg);
          if (res.data.status === 200) {
            this.prompt("提交成功", "提交个人信息");
          } else {
            this.prompt("操作失败", "提交个人信息");
          }
        });
    },
    //提交后消息框
    prompt(message, title) {
      this.$alert(message, title, {
        dangerouslyUseHTMLString: true
      });
    },
    //获取当前用户信息
    getAccountInformation() {
      let userString = sessionStorage.getItem("user");
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/user/queryUserByPhone?", {
          params: {
            phone: this.user_id
          }
        })
        .then(res => {
          console.log(res);
          this.accountInformation = res.data.data;
          if (this.accountInformation.gender === 1) {
            this.towSex = 0;
          } else {
            this.towSex = 1;
          }
          console.log(this.towSex);
        });
    }
  }
};
</script>
<style lang="scss" scoped>
.btn {
  position: absolute;
  margin: 0 auto;
  left: 50%;
  margin-left: -100px;
  top: 500px;
  height: 50px;
  width: 200px;
  border-radius: 25px;
  background-color: #bf0000;
  line-height: 50px;
  cursor: pointer;
  z-index: 2;
}
h3 {
  font-size: 25px;
  color: white;
  margin-left: 34%;
}
.to_bottom2 {
  margin-top: 50px;
  margin-left: 150px;
}
.to_bottom {
  margin-top: 200px;
  margin-left: 150px;
}
.h2_dis {
  margin-top: 50px;
  margin-left: 150px;
}
.top_left {
  width: 500px;
  height: 700px;
  position: absolute;
  left: 0;
}
.top_right {
  width: 500px;
  height: 700px;
  position: absolute;
  right: 0;
}
.personaldate {
  width: 100%;
  height: 800px;
  position: relative;
}
.title {
  width: 100%;
  text-align: center;
  font-weight: 700;
}
h1 {
  font-size: 40px;
}
h2 {
  font-weight: 600;
  font-family: Arial;
}
input {
  height: 30px;
  width: 250px;
  border: 1px solid #dcdfe6;
  margin-left: 150px;
  outline: none;
}
select {
  width: 250px;
  height: 30px;
  border: 1px solid #dcdfe6;
  margin-left: 150px;
  outline: none;
}
</style>