<template>
  <div class="payment">
    <div class="title">
      <!-- <img src="../assets/img/logo.png" alt="" width="100%" height="100%"> -->
      <p>填写核对信息</p>
    </div>
    <div class="one">
      <h1>
        收货人信息
        <a href="#">
          新增收货地址
          <i class="el-icon-arrow-right"></i>
        </a>
      </h1>
      <div class="Receiving-info">
        <P>
          <span class="name">{{accountInformation.name}}</span>
          <span class="phone">{{accountInformation.phone}}</span>
        </P>
        <P>
          <i class="el-icon-location-outline"></i>
          <span class="address">{{accountInformation.address}}</span>
        </P>
      </div>
      <div class="pay-mode">
        <h1>
          支付方式
          <a @click="checkOrder">
            查看全部订单
            <i class="el-icon-arrow-right"></i>
          </a>
        </h1>
        <button
          v-for="(item,index) in list"
          :key="index"
          v-bind:class="{'color':index===curIndex}"
          @click="select(index)"
        >{{item}}</button>
      </div>
    </div>
    <div class="submit-btn">
      <div class="count">
        <el-button type="primary" @click="dialog()">立即付款</el-button>
        <p>
          合计：￥
          <span>{{count}}</span>
        </p>
      </div>
    </div>
    <!-- 商品购买模块 -->
    <el-dialog title="" :visible.sync="dialogVisiblezfb" width="30%" :show-close="false">
      <img src="../assets/img/zfb.jpg" alt="">
      <el-button class="agree" type="primary" @click="paid">商品已支付</el-button>
      <el-button class="backOut" type="text" @click="unpaidzfb">取消支付</el-button>
    </el-dialog>
    <el-dialog title="" :visible.sync="dialogVisiblewx" width="30%" :show-close="false">
      <img src="../assets/img/wx.jpg" alt="">
      <el-button class="agree" type="primary" @click="paid">商品已支付</el-button>
      <el-button class="backOut" type="text" @click="unpaidwx">取消支付</el-button>
    </el-dialog>
    <!-- 配方交易模块 -->
    <el-dialog title="" :visible.sync=" buyFormulaDialogZfb" width="30%" :show-close="false">
      <img src="../assets/img/zfb.jpg" alt="">
      <el-button class="agree" type="primary" @click="buyFormula">配方已支付</el-button>
      <el-button class="backOut" type="text" @click="unpaidzfb">取消支付</el-button>
    </el-dialog>
    <el-dialog title="" :visible.sync="buyFormulaDialogWx" width="30%" :show-close="false">
      <img src="../assets/img/wx.jpg" alt="">
      <el-button class="agree" type="primary" @click="buyFormula">配方已支付</el-button>
      <el-button class="backOut" type="text" @click="unpaidwx">取消支付</el-button>
    </el-dialog>
  </div>
</template>



<script>
export default {
  data() {
    return {
      list: ["支付宝", "微信"],
      curIndex: 0,
      count: 0,
      flag: false,
      dialogVisiblezfb: false,
      dialogVisiblewx: false,
      shops: [],
      goodsId: [],
      accountInformation: {},
      buyFormulaDialogZfb: false,
      buyFormulaDialogWx: false
    };
  },
  created() {
    //如果有商品数据
    let str = sessionStorage.getItem("shops");
    if (str !== null) {
      this.shops = JSON.parse(str);
    }
    //一开始就计算商品总价
    this.totalCount();
    this.getAccountInformation();
    this.getFormulaTotalPrice();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    getAccountInformation() {
      let userString = sessionStorage.getItem("user");
      console.log(userString);
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/user/queryUserByPhone?", {
          params: {
            phone: this.user_id
          }
        })
        .then(res => {
          this.accountInformation = res.data.data;
        });
    },
    //点击关闭模态框
    unpaidzfb() {
      this.dialogVisiblezfb = false;
      this.buyFormulaDialogZfb = false;
    },
    unpaidwx() {
      this.dialogVisiblewx = false;
      this.buyFormulaDialogWx = false;
    },
    //商品确认支付创建订单 将每条商品的ID存入空数组 并传入后台数据库 订单创建成功 可实现查询功能
    paid() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      if (this.goodsId.length === 0) {
        this.shops.forEach(item => {
          this.goodsId.push(item.id);
        });
      } else {
        this.shops.forEach(shopitem => {
          this.goodsId.forEach(gooditem => {
            if (shopitem.id !== gooditem) this.goodsId.push(shopitem.id);
          });
        });
      }
      this.$axios
        .post("/order/addOrder", {
          user_phone: userPhone,
          payment_amount: this.count,
          payment_method: this.curIndex + 1,
          goodsIds: this.goodsId
        })
        .then(res => {
          if (res.status == 200) {
            setTimeout(() => {
              this.$router.push("/MineOrder");
            }, 2000);
          }
        });
      //创建订单后记录订单号码
    },
    //支付方式选择
    select(index) {
      this.curIndex = index;
    },
    //点击弹出扫码支付模态框
    dialog(index) {
      if (this.curIndex === 0 && !this.$route.query.id) {
        this.dialogVisiblezfb = true;
      }
      if (this.curIndex === 1 && !this.$route.query.id) {
        this.dialogVisiblewx = true;
      }
      if (this.curIndex === 0 && this.$route.query.id) {
        this.buyFormulaDialogZfb = true;
      }
      if (this.curIndex === 1 && this.$route.query.id) {
        this.buyFormulaDialogWx = true;
      }
    },
    //获取加入购物车选中商品购买总价
    totalCount() {
      //每次计算之前清空
      this.count = 0;
      this.shops.forEach(item => {
        this.count += item.subtotal;
      });
    },
    //获取配方总价
    getFormulaTotalPrice() {
      if (this.$route.query.id) {
        this.count = 0;
        this.count = this.$route.query.toatalPrice;
      }
    },
    //配方支付
    buyFormula() {
      if (this.$route.query.id) {
        let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
        this.$axios
          .post("/formula/addFormulaTransactionRecord", {
            formula_id: this.$route.query.id,
            user_phone: userPhone,
            payment_amount: Number(this.$route.query.toatalPrice),
            payment_method: this.curIndex + 1
          })
          .then(res => {
            if (res.data.status == 200) {
              //支付成功展示配方列表
              if (this.buyFormulaDialogZfb) {
                this.buyFormulaDialogZfb = false;
              } else {
                this.buyFormulaDialogWx = false;
              }
            }
          });
      }
    },
    checkOrder() {
      this.$router.push("/FormulaOrder");
    }
  },
  watch: {
    //监听路由的变化 拿到路由参数最新的值进行赋值 组件没有卸载的状态下
    $route: {
      handler(val) {
        let str = sessionStorage.getItem("shops");
        if (str !== null) {
          this.shops = JSON.parse(str);
        }
        this.totalCount();
      },
      deep: true,
      immedial: true
    }
  }
};
</script>
<style lang="scss" >
.payment {
  width: 90%;
  height: 600px;
  margin-left: 34px;
  margin-top: 60px;
  .title {
    width: 100%;
    line-height: 85px;
    border-top: 1px solid #ccc;
    margin-top: -7px;
    font-size: 20px;
    position: relative;
    img {
      width: 88px;
      height: 88px;
      position: absolute;
      top: -48px;
      left: -124px;
    }
  }
  .one {
    width: 93%;
    height: 360px;
    border: 1px solid #ccc;
    padding: 34px;
    h1 {
      width: 100%;
      height: 65px;
      font-size: 18px;
      font-weight: 600;
      a {
        display: inline-block;
        float: right;
        font-weight: 100;
        font-size: 14px;
        color: #409eff;
        cursor: pointer;
        i {
          color: #9a9a9a;
        }
      }
    }
    .Receiving-info {
      width: 100%;
      height: 145px;
      line-height: 45px;
      margin-right: 56px;
      border-bottom: 1px solid #ccc;
      .name {
        font-size: 24px;
        margin-right: 20px;
      }
      .phone {
        color: #9a9a9a;
      }
      .address {
        font-size: 16px;
      }
      i {
        font-size: 24px;
        margin-right: 18px;
      }
    }
    .pay-mode {
      width: 100%;
      height: 140px;
      margin-top: 30px;
      button {
        width: 110px;
        height: 48px;
        font-weight: 600;
        border-radius: 18px;
        margin-right: 10px;
        outline: none;
        color: #606266;
        background-color: #ebeef5;
        border: 1px solid #e4e7ed;
      }
    }
  }
  .color {
    background-color: green !important;
    color: #ffffff !important;
  }
  .submit-btn {
    width: 100%;
    height: 200px;
    position: relative;
    .count {
      width: 18%;
      font-size: 18px;
      float: right;
      position: absolute;
      top: 66px;
      right: 4px;
      display: flex;
      flex-direction: column;
      p {
        margin-top: 26px;
        text-align: center;
        span {
          font-size: 26px;
          color: #bf0000;
          font-weight: 600;
        }
      }
    }
  }
  .el-dialog {
    position: relative;
    text-align: center;
    height: 545px;
    .el-dialog__header {
      padding: 0;
    }
    img {
      width: 320px;
      height: 450px;
    }
    .el-button {
      width: 134px;
      height: 49px;
      font-size: 16px;
      font-weight: 600;
      position: absolute;
      bottom: 9px;
      right: 130px;
      background-color: #fff;
      color: #409eff;
      border: none;
    }
    .el-button--text {
      float: right;
      font-size: 16px;
      color: darkgrey;
      font-weight: 600;
      position: absolute;
      bottom: 9px;
      right: 17px;
      background-color: #fff;
    }
  }
}
</style>