<template>
  <div class="news-box" v-loading="loading">
    <!-- <div class="new-title">
      <h3>新闻推荐</h3>
    </div> -->
    <div v-for="item in list" :key="item.id">
      <div @click="go(item.id)" class="new-content">
        <div class="new-img">
          <img :src="item.imageURL" alt="">
        </div>
        <div class="new-text">
          <p class="template-title">
            <a href="javascript:;">{{item.title}}</a>
          </p>
          <p class="template-content">
            <a href="javascript:;">{{item.summary}}</a>
          </p>
          <p class="template-bar">
            <span class="template-from">
              来自主题：
              <a href="javascript:;">{{item.source}}</a>
            </span>
            <span class="template-time">
              <i class="el-icon-time"></i>
              {{item.updateTime}}
            </span>
          </p>
        </div>
      </div>
    </div>
    <div
      class="pagination"
      @click="tablePagination()"
      :class="{'show': this.number === this.count}"
    >点击加载更多</div>
  </div>
</template>

<script>
export default {
  name: "NewsTemplate",
  data() {
    return {
      list: [],
      page: "1",
      limit: 3,
      length: 2,
      count: 1,
      number: 2,
      loading:true
    };
  },
  created() {
    this.getPageData(this.page, this.limit);
    this.getData();
  },
  methods: {
    go(id) {
      this.$router.push("/news/informationDetail/" + id);
    },
    tablePagination() {
      this.getPageData(this.page, this.limit);
    },
    handleCurrentChange(val) {
      this.page = val;
    },
    getPageData(page, limit) {
      this.loading = true
      this.$axios
        .get("/article/list", {
          params: {
            page: page,
            limit: limit
          }
        })
        .then(res => {
          this.limit = this.limit + 3;
          this.list = res.data.data;
          this.number = res.data.data.length;
           this.loading = false
        });
    },
    getData() {
      this.$axios.get("/article/list").then(res => {
        this.length = this.length + res.data.data.length;
        this.count = res.data.count;
      });
    }
  }
};
</script>

<style lang="scss" scoped>
.news-box {
  padding-top: 20px;
  margin-top: 57px;
  .new-title {
    color: #262626;
    font-size: 24px;
    line-height: 33px;
    padding-bottom: 14px;
    font-weight: 500;
    h3{
      font-size: 24px;
    }
  }

  .new-content {
    margin-bottom: 30px;
    width: 98%;
    cursor: pointer;
    display: flex;
    height: 160px;
    padding: 10px;
    box-shadow: 0 0 13px rgba(0, 0, 0, 0.1);
    transition: all 0.4s;
    &:hover {
      box-shadow: 0px 5px 8px rgba(0, 0, 0, 0.5);
      transform: translate(0px, -3px);
    }

    .new-img {
      width: 216px;
      height: 100%;
      overflow: hidden;

      img {
        height: 100%;
        transition: all 1s;
        transform: scale(1);
      }
    }

    .new-text {
      height: 100%;
      margin-left: 20px;
      position: relative;
      width: calc(100% - 236px);

      .template-title {
        line-height: 24px;
        margin-top: 8px;
        margin-bottom: 10px;
        max-height: 48px;

        a {
          color: #262626;
          font-size: 18px;
          text-align: justify;
          transition: 0.5s;

          &:hover {
            color: #4285f4;
          }
        }
      }

      .template-content a {
        color: #787878;
        font-size: 13px;
        letter-spacing: 0;
        line-height: 20px;
        margin-top: 10px;
        max-height: 40px;

        &:hover {
          color: #797979;
        }
      }

      .template-bar {
        bottom: 2px;
        color: #a7a7a7;
        float: left;
        font-size: 12px;
        line-height: 18px;
        position: absolute;
        width: 100%;

        .template-from {
          padding-right: 6px;

          a {
            color: #007bff;
          }
        }

        .template-time {
          float: right;
          padding-left: 5px;
        }
      }
    }
  }
  .pagination {
    position: relative;
    top: 30px;
    left: 440px;
    height: 36px;
    width: 150px;
    border: 2px solid #007bff;
    line-height: 36px;
    text-align: center;
    color: #007bff;
    cursor: pointer;
  }
  .show {
    display: none;
  }
}
</style>
