<template>
  <div class="sell">
    <el-steps :active="active" finish-status="success">
      <el-step title="第一步" description="上传配方"></el-step>
      <el-step title="第二步" description="上传原料处理"></el-step>
      <el-step title="第三步" description="上传炒料过程"></el-step>
      <el-step title="第四步" description="上传煮锅方法"></el-step>
      <el-step title="第五步" description="邮寄样品"></el-step>
      <!-- <el-step title="第六步" description="等待审核"></el-step> -->
    </el-steps>
    <!-- 上传配方 -->
    <div class="formSell" v-show="flag1">
      <h1>上传配方</h1>
      <div class="right">
        <h3>配方名称：</h3>
        <el-input v-model="materialName" placeholder="配方名称" @change="changeInput"></el-input>
        <div class="inputBox">
          <el-input v-model="obj.raw_material_name" placeholder="原材料"></el-input>
          <el-input v-model="obj.variety" placeholder="品种"></el-input>
          <el-input v-model="obj.weight" placeholder="重量" style="width:14%"></el-input>
          <el-button type="primary" @click="handleAdd">添加</el-button>
        </div>
        <el-table :data="tabData" style="width: 80%">
          <el-table-column label="原材料" width="180">
            <template slot-scope="scope">
              <span>{{scope.row.raw_material_name}}</span>
            </template>
          </el-table-column>
          <el-table-column label="品种" width="180">
            <template slot-scope="scope">
              <span>{{scope.row.variety}}</span>
            </template>
          </el-table-column>
          <el-table-column label="重量" width="180">
            <template slot-scope="scope">
              <span>{{scope.row.weight}}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button size="mini" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
            </template>
          </el-table-column>
        </el-table>
        <textarea placeholder="添加自行描述" v-model="desc" @change="getTxt1"></textarea>
      </div>
    </div>
    <!-- 上传原料处理方式 -->
    <div class="materialtreat" v-show="flag2">
      <h1>上传原料处理方式</h1>
      <div class="right">
        <div class="inputBox">
          <el-input v-model="obj1.raw_material_name" placeholder="原材料" @change="changeInput"></el-input>
          <el-input v-model="obj1.methods" placeholder="处理方式" @change="changeInput"></el-input>
          <el-button type="primary" @click="handleAdd1">添加</el-button>
        </div>
        <el-table :data="tabData1" style="width: 80%">
          <el-table-column label="原材料" width="180">
            <template slot-scope="scope">
              <span>{{scope.row.raw_material_name}}</span>
            </template>
          </el-table-column>
          <el-table-column label="处理方式" width="340">
            <template slot-scope="scope">
              <span>{{scope.row.methods}}</span>
            </template>
          </el-table-column>
          <el-table-column label="操作">
            <template slot-scope="scope">
              <el-button
                size="mini"
                type="danger"
                @click="handleDelete1(scope.$index, scope.row)"
              >删除</el-button>
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <!-- 上传炒料过程 -->
    <div class="frymaterial" v-show="flag3">
      <h1>上传炒料过程</h1>
      <div class="right">
        <textarea placeholder="第一步：加入XXX(原材料)，炒制XX分钟,温度XX..." v-model="frypro" @change="getTxt3"></textarea>
      </div>
    </div>
    <!-- 上传煮锅方法 -->
    <div class="cookmethod" v-show="flag4">
      <h1>上传煮锅方法</h1>
      <div class="right">
        <textarea placeholder="第一步：加入XXX,煮锅XX分钟,锅内状态" v-model="cookway" @change="getTxt4"></textarea>
      </div>
    </div>
    <!-- 邮寄样品 -->
    <div class="post" v-show="flag5">
      <h1>邮寄样品</h1>
      <div class="right">
        <textarea
          placeholder="
          成都川料智选科技有限公司 
          地址：成都市武侯区科华北路99号四川大学国家大学科技园 
          电话：028-85533714/400-8852078"
        ></textarea>
      </div>
    </div>
    <!-- 等待审核 -->
    <div class="verify" v-show="flag6">
      <h1>等待审核</h1>
      <div class="right">
        <div class="btn_tips">平台正在审核中,请您耐心等待</div>
      </div>
    </div>
    <div class="btn_pre" @click="preStep" v-show="btnPreShow">上一步</div>
    <div class="btn_next" @click="next" v-show="btnShow">下一步</div>
    <div class="btn_next" @click="submitData" v-show="subShow">确认提交</div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      active: 0,
      flag1: true,
      desc: "",
      flag2: false,
      flag3: false,
      frypro: "",
      flag4: false,
      cookway: "",
      flag5: false,
      postaddress: "",
      flag6: false,
      tabData: [],
      obj: {
        raw_material_name: "",
        variety: "",
        weight: ""
      },
      tabData1: [],
      obj1: {
        raw_material_name: "",
        methods: ""
      },
      materialName: "",
      btnShow: true,
      subShow: false,
      btnPreShow: true,
      rawMaterial: []
    };
  },
  created() {
    //数据初始化之后重新取出数据再赋值给表格渲染的数据
    let formula = localStorage.getItem("formula");
    let handle = localStorage.getItem("handle");
    if (formula !== null) {
      this.tabData = JSON.parse(formula);
    }
    if (handle !== null) {
      this.tabData1 = JSON.parse(handle);
    }
  },
  methods: {
    next() {
      //变颜色的为当前下标 到第六个变颜色的时候 第一个变颜色
      console.log(this.active);
      if (this.active++ > 5) this.active = 0;
      if (this.active === 0) {
        this.flag1 = true;
        this.flag6 = false;
      }
      if (this.active === 5) {
        this.flag6 = true;
        this.flag5 = false;
        this.flag2 = false;
        this.flag3 = false;
        this.flag4 = false;
        this.btnPreShow = false;
        this.btnShow = false;
        this.subShow = false;
      }
      if (this.active === 4) {
        this.flag5 = true;
        this.flag4 = false;
        this.flag6 = false;
        this.flag2 = false;
        this.flag3 = false;
        this.subShow = true;
        this.btnShow = false;
      }
      if (this.active === 3) {
        this.flag2 = false;
        this.flag4 = true;
        this.flag3 = false;
        this.flag5 = false;
      }
      if (this.active === 2) {
        this.flag3 = true;
        this.flag2 = false;
        this.flag4 = false;
        this.flag1 = false;
      }
      if (this.active === 1) {
        this.flag2 = true;
        this.flag1 = false;
        this.flag3 = false;
      }
    },
    preStep() {
      if (this.active-- < 1) this.active = 0;

      if (this.active === 0) {
        this.flag1 = true;
        this.flag2 = false;
        this.flag6 = false;
        this.subShow = false;
        this.btnShow = true;
      }
      if (this.active === 1) {
        this.flag2 = true;
        this.flag1 = false;
        this.flag3 = false;
        this.subShow = false;
        this.btnShow = true;
      }
      if (this.active === 2) {
        this.flag3 = true;
        this.flag2 = false;
        this.flag4 = false;
        this.subShow = false;
        this.btnShow = true;
      }
      if (this.active === 3) {
        this.flag4 = true;
        this.flag3 = false;
        this.flag5 = false;
        this.subShow = false;
        this.btnShow = true;
      }
      if (this.active === 4) {
        this.flag5 = true;
        this.flag4 = false;
        this.flag6 = false;
        this.btnShow = true;
        this.subShow = true;
      }
      if (this.active === 5) {
        this.flag6 = true;
        this.flag5 = false;
        this.flag1 = false;
        this.btnShow = false;
        this.subShow = false;
      }
      if (this.active === 6) {
        this.flag6 = true;
        this.flag1 = false;
        this.flag2 = false;
        this.btnPreShow = false;
        this.btnShow = false;
        this.subShow = false;
      }
    },
    changeInput() {
      localStorage.setItem("materialName", JSON.stringify(this.materialName));
    },
    //添加到表格将数组存本地缓存
    handleAdd() {
      if (
        this.obj.raw_material_name == null ||
        this.obj.variety == null ||
        this.obj.weight == null || this.obj.weight ==0
      ) {
        this.$message({
          message: "不能为空",
          type: "warning"
        });
      } else {
        this.tabData.push(this.obj);
        this.obj = {};
        var arr2 = this.tabData.map(item => {
          var num = Number(item.weight);
          item["weight"] = num;
          return item;
        });
        localStorage.setItem("formula", JSON.stringify(arr2));
      }
    },
    //删除表格更新本地缓存
    handleDelete(index, row) {
      this.tabData.forEach(item => {
        if (item.raw_material_name == row.raw_material_name) {
          this.tabData.splice(index, 1);
          localStorage.setItem("formula", JSON.stringify(this.tabData));
        }
      });
    },
    //添加表格
    handleAdd1() {
      if (this.obj.raw_material_name == null ||
        this.obj.methods == null) {
        this.$message({
          message: "不能为空",
          type: "warning"
        });
      } else {
        this.tabData1.push(this.obj1);
        this.obj1 = {};
        localStorage.setItem("handle", JSON.stringify(this.tabData1));
      }
    },
    //删除表格
    handleDelete1(index, row) {
      this.tabData1.forEach(item => {
        if (item.raw_material_name == row.raw_material_name) {
          this.tabData1.splice(index, 1);
          localStorage.setItem("handle", JSON.stringify(this.tabData1));
        }
      });
    },
    getTxt1() {
      localStorage.setItem("description", JSON.stringify(this.desc));
    },
    getTxt3() {
      let fryingmaterial = this.frypro;
      localStorage.setItem("fry", JSON.stringify(fryingmaterial));
    },
    getTxt4() {
      let cookway = this.cookway;
      localStorage.setItem("cook", JSON.stringify(cookway));
    },
    //提交信息
    submitData() {
      const formula = JSON.parse(localStorage.getItem("formula"));
      const handle = JSON.parse(localStorage.getItem("handle"));
      const fry = JSON.parse(localStorage.getItem("fry"));
      const cook = JSON.parse(localStorage.getItem("cook"));
      const materialName = JSON.parse(localStorage.getItem("materialName"));
      const description = JSON.parse(localStorage.getItem("description"));
      console.log(description);
      for (var i = 0; i < formula.length; i++) {
        for (var j = 0; j < handle.length; j++) {
          if (formula[i].raw_material_name == handle[j].raw_material_name) {
            formula[i]["processing_method"] = handle[j].methods;
            this.rawMaterial.push(formula[i]);
          }
        }
      }
      this.uploadFormula(materialName, fry, cook, description);
    },
    //上传配方
    uploadFormula(materialName, fry, cook, description) {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("/formula/addFormulaUpload", {
          formula_upload_name: materialName,
          user_phone: userPhone,
          flour_process: fry,
          cooking_pot_method: cook,
          description: description,
          raw_materials: this.rawMaterial
        })
        .then(res => {
          console.log(res.data);
          if (res.data.status == 200) {
            this.active = 5;
            this.flag5 = false;
            this.flag6 = true;
            this.btnPreShow = false;
            this.btnShow = false;
            this.subShow = false;
            this.removeCache();
          }
        });
    },
    //清空缓存
    removeCache() {
      localStorage.removeItem("formula");
      localStorage.removeItem("handle");
      localStorage.removeItem("fry");
      localStorage.removeItem("cook");
      localStorage.removeItem("materialName");
      localStorage.removeItem("description");
    }
  }
};
</script>

<style lang="scss">
.active {
  background-color: #b1cc64;
  color: #b1cc64;
}
.sell {
  width: 100%;
  min-height: 180px;
  position: relative;
  margin-top: 142px;
  margin-left: 28px;
  .el-steps {
    margin: 0 auto;
    width: 97%;
    position: relative;
    .el-step {
      width: 138px;
      height: 70px;
      .el-step__main {
        position: absolute;
        top: -70px;
        left: -22px;
        .el-step__title {
          font-size: 16px;
          color: #a30014;
        }
        .el-step__description {
          width: 98px;
          font-size: 16px;
        }
      }
    }
    .el-step__head {
      .el-step__line {
        height: 14px;
        border-radius: 4px;
        position: relative;
        .el-step__line-inner {
          height: 100%;
          background-color: #b1cc64;
          border: none;
        }
      }
      .el-step__icon {
        width: 32px;
        height: 32px;
        position: absolute;
        left: -9px;
        top: 0px;
      }
    }
  }
  .formSell {
    margin-top: 20px;
    h1 {
      width: 10%;
      height: 75px;
      padding: 20px 0;
      font-size: 20px;
      float: left;
    }
    .right {
      float: right;
      width: 84%;
      .el-input {
        width: 25%;
        margin-right: 3px;
        margin-top: 30px;
        .el-input__inner {
          height: 40px;
          outline: none;
          border-radius: 0px;
        }
      }
      .el-table {
        margin-top: 40px;
        .el-table__header thead tr th {
          font-size: 16px;
        }
        .el-table__body tbody .el-table__row td {
          background-color: #fff !important;
          padding: 0px;
          height: 65px;
        }
      }
      textarea {
        width: 79%;
        height: 120px;
        margin-top: 5px;
        font-size: 18px;
        outline: none;
      }
    }
    .inputBox {
      width: 80%;
      height: 40px;
      .el-input {
        width: 31%;
        margin-right: 3px;
        .el-input__inner {
          height: 40px;
          outline: none;
          border-radius: 0px;
        }
      }
      .el-button {
        border-radius: 0px;
      }
    }
  }
  .materialtreat {
    @extend .formSell;
    h1 {
      width: 16%;
    }
    .right {
      width: 79%;
      .inputBox {
        .el-input:nth-child(2) {
          width: 47%;
        }
      }

      .el-table__body-wrapper {
        border: none;
      }
    }
  }
  .frymaterial {
    @extend .formSell;
    h1 {
      width: 16%;
    }
    .right {
      float: right;
      width: 79%;
      .el-table__body-wrapper {
        border: none;
      }
    }
  }
  .cookmethod {
    @extend .frymaterial;
  }
  .post {
    @extend .cookmethod;
    h1 {
      width: 10%;
    }
    .right {
      width: 89%;
    }
  }
  .verify {
    @extend .post;
    .right {
      width: 89%;
      > div {
        width: 682px;
        height: 225px;
        margin-top: 5px;
      }
      .btn_tips {
        border: 2px solid #cccccc;
        text-align: center;
        font-size: 22px;
        font-weight: 600;
        color: #bf0000;
        line-height: 225px;
      }
    }
  }
  .btn_next {
    width: 145px;
    height: 54px;
    color: white;
    background-color: #bf0000;
    text-align: center;
    line-height: 54px;
    margin-left: 190px;
    cursor: pointer;
    border-radius: 4px;
    position: absolute;
    bottom: -68px;
    left: 264px;
  }
  .btn_pre {
    @extend .btn_next;
    left: 100px;
  }
}
</style>
