<template>
  <div class="minemessage">
    <div class="title">
      <h1>我的川料</h1>
    </div>
    <div class="d_span" style="padding-bottom:15px;">
      <span
        v-for="(item,index) in messages"
        :key="index"
        @click="change(index)"
        :class="{'font':index===curIndex}"
      >{{item}}</span>
    </div>
    <div v-show="!flag">
      <div class="thisIsLift" :class="{'show':this.countTow !== 0}">暂时没有任何个人留言</div>
      <div v-for="item in oneList" :key="item.id">
        <el-card class="box-card" style="margin-bottom:20px;">
          <div slot="header" class="clearfix">
            <span>我的个人留言</span>
          </div>&nbsp; &nbsp; &nbsp; &nbsp;
          <span>{{item.content}}</span>
          <div style="padding-top:10px;font-size:12px;text-align:right">{{item.create_time}}</div>
        </el-card>
      </div>
      <div class="pagination" :class="{'show': this.countTow === this.oneList.length}">
        <el-button type="success" round @click="setMessage()">加载更早的个人留言</el-button>
      </div>
    </div>
    <div v-show="flag">
      <div class="thisIsLift" :class="{'show':this.countOne !== 0}">暂时没有任何系统消息</div>
      <div v-for="item in towList" :key="item.id">
        <el-card class="box-card" style="margin-bottom:20px;">
          <div slot="header" class="clearfix">
            <span>系统消息</span>
          </div>&nbsp; &nbsp; &nbsp; &nbsp;
          <span>{{item.content}}</span>
          <div style="padding-top:10px;font-size:12px;text-align:right">{{item.create_time}}</div>
        </el-card>
      </div>
      <div
        class="pagination"
        @click="setSystemMessage()"
        :class="{'show': this.countOne === this.towList.length}"
      >
        <el-button type="success" round @click="setSystemMessage()">加载更早的系统消息</el-button>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      messages: ["我的留言", "系统消息"],
      curIndex: 0,
      flag: false,
      oneList: [
        {
          content: "",
          create_time: ""
        }
      ],
      towList: [],
      pageOne: 1,
      limitOne: 2,
      lengthOne: 2,
      countOne: 1,
      number: 2,
      pageTow: 1,
      limitTow: 2,
      lengthTow: 2,
      countTow: 1
    };
  },
  created() {
    this.setMessage();
    this.setSystemMessage();
  },
  methods: {
    change(index) {
      this.curIndex = index;
      if (index === 0) {
        this.flag = false;
      } else {
        this.flag = true;
      }
    },
    setMessage() {
      let userString = sessionStorage.getItem("user");
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/message/queryMessageList?", {
          params: {
            page: this.pageTow,
            limit: this.limitTow,
            user_phone: this.user_id,
            type: "1"
          }
        })
        .then(res => {
          console.log(res.data.data);
          this.limitTow = this.limitTow + this.number;
          this.oneList = res.data.data;
          this.countTow = res.data.count;
          console.log(this.oneList);
        });
    },
    setSystemMessage() {
      let userString = sessionStorage.getItem("user");
      this.user_id = JSON.parse(userString).phone;
      this.$axios
        .get("/message/queryMessageList?", {
          params: {
            page: this.pageOne,
            limit: this.limitOne,
            type: "0"
          }
        })
        .then(res => {
          this.limitOne = this.limitOne + this.number;
          this.towList = res.data.data;
          this.countOne = res.data.count;
          console.log(this.towList);
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.font {
  color: #bf0000;
}
.box_1 {
  position: relative;
  width: 90%;
  height: 150px;
  border: 1px solid gray;
  border-radius: 30px;
  margin-left: 40px;
  margin-top: 50px;
  text-align: center;
  padding: 10px 10px;
  line-height: 20px;
  span {
    position: absolute;
    right: 15px;
    bottom: 15px;
    color: #a9a9a9;
  }
  .content {
    margin-top: 20px;
    width: 100%;
  }
}
.thisIsLift {
  position: relative;
  width: 90%;
  border: 1px solid gray;
  border-radius: 30px;
  margin-left: 40px;
  margin-top: 50px;
  text-align: center;
  padding: 10px 10px;
  line-height: 20px;
}
.box_2 {
  position: relative;
  margin-top: 25px;
  width: 90%;
  height: 150px;
  border: 1px solid gray;
  border-radius: 30px;
  margin-left: 40px;
  margin-top: 50px;
  text-align: center;
  padding: 10px 10px;
  line-height: 20px;
  span {
    position: absolute;
    right: 15px;
    bottom: 15px;
    color: #a9a9a9;
  }
  .content {
    width: 100%;
    margin-top: 20px;
  }
}
.d_span {
  margin-top: 70px;
  span {
    font-size: 25px;
    margin-right: 50px;
    cursor: pointer;
  }
}
.minemessage {
  width: 100%;
}
.title {
  width: 100%;
  text-align: center;
  font-weight: 700;
}
h1 {
  font-size: 40px;
}
.pagination {
  position: relative;
  top: 20px;
  left: 440px;
  height: 36px;
  width: 200px;
  cursor: pointer;
}
.show {
  display: none;
}
.el-card .is-always-shadow .el-card__body {
  height: 300px;
}
.box-card {
  font-family: "微软雅黑";
  letter-spacing: 1px;
  span {
    height: 26px;
    line-height: 26px;
  }
}
</style>