<template>
  <div class="wrap">
    <div class="detail">
      <ul class="left-smallImg">
        <li v-for="item in detail.images" :key="item.id">
          <img :src="item" alt="商品示例" width="100%" height="100%">
        </li>
      </ul>
      <div class="zoom">
        <div class="smallImg">
          <img :src="detail.bigPicture" alt="商品展示">
          <span class="zoomspan"></span>
          <div class="decs">
            <h1>{{this.detail.formulaName}}</h1>
            <p class="text">
              说明：
              <span>{{this.detail.formulaDescription}}</span>
            </p>
            <p class="price">{{this.detail.formulaPrice}}元</p>
          </div>
        </div>
        <div class="bigImg">
          <img :src="detail.bigPicture" alt="商品展示" class="pic">
        </div>
      </div>
    </div>
    <div class="middle">
      <el-steps :space="200" :active="active" finish-status="success">
        <el-step title="试经营"></el-step>
        <el-step title="确认购买" class="up"></el-step>
        <el-step title="日期预约" id="maodian"></el-step>
        <el-step title="料场实操" class="up"></el-step>
        <el-step title="售后确认"></el-step>
        <el-step title="学习结束" class="up"></el-step>
      </el-steps>
      <el-button class="dialog-btn1" type="text" @click="dialogVisible1 = true"></el-button>
      <el-button class="dialog-btn2" type="text" @click="dialogVisible2 = true"></el-button>
      <el-button class="dialog-btn3" type="text" @click="dialog3"></el-button>
      <el-button class="dialog-btn4" type="text" @click="dialogVisible4 = true"></el-button>
      <el-button class="dialog-btn5" type="text" @click="dialogVisible5 = true"></el-button>
      <el-button class="dialog-btn6" type="text" @click="dialogVisible6 = true"></el-button>
      <!-- 试经营 -->
      <el-dialog title="" :visible.sync="dialogVisible1" @click="dialogVisible1 = false">
        <span class="commodity-info">
          <div class="bigImg">
            <img :src="materialDetail.bigImage" alt="商品展示">
          </div>
          <div class="decs">
            <h1>{{materialDetail.tradeName}}</h1>
            <p class="text">规格：{{materialDetail.content}}</p>
            <p class="price">{{materialDetail.price}}元-{{materialDetail.price}}元</p>
            <p>
              <button @click="addToCart">加入购物车</button>
              <!-- <button class="green">一键购</button> -->
            </p>
            <!-- <img src="../assets/img/top-cart.png"> -->
          </div>
        </span>
      </el-dialog>
      <!-- 确认购买 -->
      <el-dialog title="" :visible.sync="dialogVisible2" @click="dialogVisible2 = false">
        <span class="commodity-info">
          <div class="bigImg">
            <img :src="this.detail.bigPicture" alt="商品展示">
          </div>
          <div class="decs">
            <h1>{{this.detail.formulaName}}</h1>
            <p class="text">规格：{{this.detail.formulaDescription}}</p>
            <p class="price">{{this.detail.formulaPrice}}元-{{this.detail.formulaPrice}}元</p>
            <p>
              <button @click="go">立即购买</button>
              <!-- <button class="green">一键购</button> -->
            </p>
            <!-- <img src="../assets/img/top-cart.png"> -->
          </div>
          <!-- <span slot="footer" class="dialog-footer" @click="dialogVisible2 = false"></span> -->
        </span>
      </el-dialog>
      <!-- 预约时间 -->
      <el-dialog
        class="data-dialog"
        title="预约日期"
        :visible.sync="dialogVisible3"
        @click="dialogVisible3 = false"
      >
        <Calendar @choseDay="clickDay" :agoDayHide="curData" :markDateMore="noAppointTime"></Calendar>
        <el-button type="primary" class="confirm" @click="confirmTime()">确认</el-button>
      </el-dialog>
    </div>
    <div class="product-description">
      <!-- <ul class="btn">
        <li
          v-for="(item,index) in title"
          :key="index"
          @click="change(index)"
          :class="{'tab':index===curIndex}"
        >{{item}}</li>
      </ul>-->
      <hr>
      <!-- <p>随着近几年餐饮行业的发展，整个餐饮行业收入更是节节攀升，看着餐饮行业大好的市场，不少的人投入到餐饮行业中来，产品挑选则是要进入餐饮行业面临的一大难题。在火锅底料批发中为什么味道是竞争力?赝品从来不会得到人们青睐，这是大多数人找美蛙鱼头底料配料生产的原因之一。这就和当地的饮食消费习惯密切相关了。你的店开在外地，但味道确实是好的火锅味道，因为不符合当地人的口味，再好的味道也很难得到消费者认可。所以你在开店筹备寻找火锅底料批发厂家的过程中，要先了解当地人的消费爱好，找能符合当地人消费习惯的火锅底料味道。</p> -->
      <h3>
        <a href="javascript:;" style="color: black">售后服务专区:</a>
      </h3>
      <br>
      <p></p>
      <h4>
        <a href="javascript:;" @click="learnAgain()">再次学习|</a>
        <a href="javascript:;">底料代炒|</a>
        <a href="javascript:;">协助开店</a>
      </h4>
      <p></p>
    </div>
  </div>
</template>

<script>
import Calendar from "vue-calendar-component";
export default {
  components: {
    Calendar
  },
  data() {
    return {
      active: 0,
      detail: {},
      dialogVisible1: false,
      dialogVisible2: false,
      dialogVisible3: false,
      num: 1,
      checked: false,
      remove: false,
      newDetail: {},
      baseStoreId: "",
      materialDetail: {},
      title: ["描述", "其他相关"],
      curIndex: 0,
      curData: "",
      allowAppointTime: [],
      noAppointTime: [],
      id: ""
    };
  },

  created() {
    this.getData();
    this.disabledDate();
  },
  mounted() {
    this.zoomPic();
  },
  //清除页面缓存
  deactivated() {
    this.$destroy();
  },
  methods: {
    //底料加入购物车
    addToCart() {
      let newDetail = {
        ...this.detail,
        tradeName: this.materialDetail.tradeName,
        price: this.materialDetail.price,
        img: this.materialDetail.bigImage,
        num: this.num,
        checked: this.checked,
        remove: this.remove
      };
      this.newDetail = newDetail;
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请先登录再购买",
          type: "warning"
        });
      } else {
        this.addGoods();
      }
    },
    addGoods() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("/order/addGoods", {
          user_phone: userPhone,
          goods_price: this.newDetail.price,
          goods_name: this.newDetail.tradeName,
          goods_img: this.newDetail.img,
          goods_count: this.newDetail.num,
          subtotal: this.newDetail.price * this.newDetail.num
        })
        .then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: "恭喜你，商品已经成功添加",
              type: "success"
            });
            setTimeout(() => {
              this.dialogVisible1 = false;
              this.active = 1;
            }, 1000);
          }
        });
    },
    //获取配方详情
    getData() {
      this.$axios
        .get("/formula/details", {
          params: {
            id: this.$route.params.id
          }
        })
        .then(res => {
          this.detail = res.data;
          console.log(this.detail);
          if (sessionStorage.getItem("user")) {
            this.findFormula();
          }
          //不可预约日期对象格式化传入数组 引用类名禁用
          this.detail.practical_operation_noAppointment.forEach(item => {
            let obj = {
              date: item,
              className: "mark1"
            };
            this.noAppointTime.push(obj);
          });
          this.baseStoreId = res.data.baseStoreId;
          this.getMaterialDetail();
        });
    },
    //获取底料详情
    getMaterialDetail() {
      this.$axios
        .get("/baseTrade/details", {
          params: {
            id: this.baseStoreId
          }
        })
        .then(res => {
          this.materialDetail = res.data;
        });
    },
    //已够买配方查询
    findFormula() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .get("/formula/queryUserFormulaByUserAndFormula", {
          params: {
            user_phone: userPhone,
            formula_id: this.$route.params.id
          }
        })
        .then(res => {
          console.log(res);
          if (res.data.data !== null) {
            this.id = res.data.data.id;
            if (res.data.status == 200 && res.data.data) {
              this.active = 2;
            }
            if (res.data.data.practical_operation_appointment !== null) {
              this.active = 3;
            }
          }
        });
    },
    //点击购买配方
    go() {
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请先登录再购买",
          type: "warning"
        });
      } else {
        this.dialogVisible2 = false;
        this.$router.push({
          path: "/payment",
          query: {
            toatalPrice: this.detail.formulaPrice,
            id: this.detail.id
          }
        });
      }
    },
    //日期预约提示
    dialog3() {
      //如果第二步完成载进行第三步
      if (this.active == 2) {
        this.dialogVisible3 = true;
      } else {
        this.$message({
          message: "请先购买配方",
          type: "warning"
        });
      }
    },
    //选项卡
    change(index) {
      this.curIndex = index;
    },
    //步骤条
    next() {
      if (this.active++ > 5) this.active = 0;
    },
    //当天日期之前的日期不可选
    disabledDate() {
      this.curData = String(
        new Date(new Date().setHours(0, 0, 0, 0)).getTime()
      ).slice(0, -3);
    },
    //选取可预约日期[数组]
    clickDay(data) {
      console.log(data);
      let dataArr = [];
      this.noAppointTime.forEach(item => {
        dataArr.push(item.date);
      });
      let isIncludes = dataArr.includes(data);
      if (isIncludes) {
        this.$message({
          message: "该日期不可预约",
          type: "warning"
        });
      } else {
        this.allowAppointTime.push(data);
      }
    },
    //确认预约日期
    confirmTime(a, data) {
      if (this.active == 2) {
        a = 1;
      } else {
        a = 2;
      }
      this.$axios
        .post("/formula/addAppointment", {
          appointment_type: a,
          id: this.id,
          date: this.allowAppointTime[0]
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            this.$message({
              message: "预约成功",
              type: "success"
            });
            this.dialogVisible3 = false;
            //查询操作进度
            this.findFormula();
          }
        });
    },
    //初次学习完成再次学习
    learnAgain() {
      if (this.active == 6) {
        this.dialogVisible3 = true;
      } else {
        this.$message({
          message: "请先进行初次学习",
          type: "warning"
        });
      }
    }
  },
  watch: {
    $route: {
      handler(n) {
        this.getData();
        this.findFormula();
      },
      deep: true,
      immedial: true
    },
    active: function(value) {}
  }
};
</script>
<style lang='scss'>
