    }
    .dialog-btn5 {
      @extend .dialog-btn1;
      top: -20px;
      left: 589px;
    }
    .dialog-btn6 {
      @extend .dialog-btn1;
      top: 180px;
      left: 742px;
    }
    .next {
      width: 13%;
    }
    //模态框
    .el-dialog {
      width: 60% !important;
