<template>
  <div class="modal-content">
    <header>
      <img class="logo" src="@/assets/img/logo.png" alt="">
    </header>
    <!-- 注册模态框主体 -->
    <el-form
      :model="ruleForm"
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm modal-body"
    >
      <el-form-item label="" prop="phone" class="input-area">
        <el-input type="text" placeholder="请输入手机号" v-model="ruleForm.phone" autocomplete="off"></el-input>
      </el-form-item>
      <el-form-item label="" prop="password" class="input-area">
        <el-input
          type="password"
          placeholder="请输入密码（6-16位数组与字母）"
          v-model="ruleForm.password"
          autocomplete="off"
        ></el-input>
      </el-form-item>
      <el-form-item label="" prop="code" class="input-area third">
        <el-input type="text" placeholder="验证码" v-model="ruleForm.code" autocomplete="off"></el-input>
        <!-- <el-button class="code" @click="Verifiy()">{{ruleForm.verifiyBtn}}</el-button> -->

        <el-button
          class="code"
          type="button"
          @click="sendcode"
          :disabled="disabled"
          v-if="disabled==false"
        >发送验证码</el-button>
        <el-button
          class="code"
          type="button"
          @click="sendcode"
          :disabled="disabled"
          v-if="disabled==true"
        >{{btntxt}}</el-button>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" class="submit" @click="submitForm('ruleForm')">注册</el-button>
      </el-form-item>
    </el-form>
    <footer>
      已有账号？
      <span class="change-login" @click="go">去登录</span>
    </footer>
  </div>
</template>

<script>
export default {
  name: "Register",
  data() {
    var phone = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入手机号码"));
      } else {
        const reg = /^1[3|4|5|7|8][0-9]\d{8}$/;
        console.log(reg.test(value));
        if (reg.test(value)) {
          callback();
        } else {
          return callback(new Error("请输入正确的手机号"));
        }
      }
    };
    var password = (rule, value, callback) => {
      if (!value) {
        callback(new Error("请输入密码"));
      } else {
        const reg = /^(?![0-9]+$)(?![a-zA-Z]+$)[0-9A-Za-z]{6,16}$/;
        console.log(reg.test(value));
        if (reg.test(value)) {
          callback();
        } else {
          return callback(new Error("密码格式错误"));
        }
      }
    };
    var code = (rule, value, callback) => {
      if (value) {
        callback();
      } else {
        return callback(new Error("请输入验证码"));
      }
    };

    return {
      disabled: false,
      time: 0,
      btntxt: "重新发送",
      ruleForm: {
        phone: "",
        password: "",
        code: "",
        verifiyBtn: "获取验证码"
      },
      rules: {
        phone: [{ validator: phone, trigger: "blur" }],
        password: [{ validator: password, trigger: "blur" }],
        code: [{ validator: code, trigger: "blur" }]
      }
    };
  },

  methods: {
    sendcode() {
      this.$axios
        .post("/front/sendSmsRegisterCode", {
          phone: this.ruleForm.phone
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            this.time = 60;
            this.disabled = true;
            this.timer(); 
          }
        });
    },
    timer() {
      if (this.time > 0) {
        this.time--;
        this.btntxt = this.time + "s后重新获取";
        setTimeout(this.timer, 1000);
      } else {
        this.time = 0;
        this.btntxt = "获取验证码";
        this.disabled = false;
      }
    },
    closeDialog() {
      this.$emit("closeDialog", false);

    },
    register() {
      this.$axios
        .post("/front/register", {
          code: this.ruleForm.code,
          phone: this.ruleForm.phone,
          password: this.ruleForm.password
        })
        .then(res => {
          console.log(res);
          if (res.data.status == 200) {
            this.$message({
              message: "恭喜您！注册成功",
              type: "success"
            });
            this.closeDialog();
          } else {
            this.$message({
              message: "验证码错误",
              type: "warning"
            });
          }
        });
    },
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.register();
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    resetForm() {
      this.$refs.ruleForm.resetFields();
    },
    go() {
      this.closeDialog();
    }
    //校验表单 参数9是一个回调函数 该函数会在校验结束后被调用 参数1是否校验成功、
    //参数2未通过校验的字段 若不传入该回调函数，则会返回一个 promise
  }
};
</script>

<style lang="scss" scoped >
.modal-content {
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 5px 15px 0 rgba(0, 0, 0, 0.15);
  font-size: 16px;
  header {
    height: 110px;
    .logo {
      margin-left: 198px;
      margin-top: 30px;
      width: 64px;
    }
  }
  .modal-body {
    text-align: center;
    margin: 0 auto;
    margin-top: 10px;
    .el-form-item {
      width: 85%;
      outline: none;
      /deep/.el-form-item__content {
        margin-left: 70px !important;
        /deep/.el-input {
          height: 56px;
          color: #262626;
          font-size: 16px;
          .el-input__inner {
            width: 100%;
            height: 80%;
            border-radius: 0px;
            outline: none;
            border: none;
            border-bottom: 1px solid #dcdfe6;
          }
        }
      }
    }
    .el-form-item:nth-child(3) {
      .el-input {
        width: 65%;
      }
      /deep/.code {
        width: 35%;
        height: 56px;
        text-align: center;
        outline: none;
        border: none;
        background-color: #fff;
        &:hover {
          border: none;
          background-color: #fff;
        }
      }
    }
    .submit {
      width: 100%;
      height: 100%;
    }
  }
  footer {
    color: #a7a7a7;
    font-size: 14px;
    padding-bottom: 40px;
    padding-top: 20px;
    text-align: center;
    span {
      color: #4285f4;
      cursor: pointer;
    }
  }
}
</style>
