<template>
  <div class="wrap-materialdetail" v-loading="loading">
    <div class="materialdetail">
      <ul class="left-smallImg">
        <li v-for="item in detail.images" :key="item.id">
          <img :src="item" alt="商品示例" width="100%" height="100%">
        </li>
      </ul>
      <div class="zoom">
        <div class="smallImg">
          <img :src="detail.bigImage" alt="商品展示">
          <span class="zoomspan"></span>
          <div class="decs">
            <h1>{{detail.tradeName}}</h1>
            <p class="text">说明：{{detail.label}}</p>
            <p class="price">{{detail.price}}元</p>
            <el-form ref="form">
              <el-form-item label="商品数量" prop="name">
                <el-input-number v-model="num" :min="1"></el-input-number>
              </el-form-item>
            </el-form>
            <p>
              <button @click="addToCart()">加入购物车</button>
              <!-- <button class="green">一键购</button> -->
            </p>
          </div>
        </div>
        <div class="bigImg">
          <img :src="detail.bigImage" alt="商品展示" class="pic">
        </div>
      </div>
    </div>
    <div class="comment">
      <button>评论</button>
    </div>
    <div class="message">
      <div class="textarea">
        <p>
          <span>姓名</span>
          <span>评论日期</span>
        </p>
        <textarea
          class="critical"
          ref="submitContent"
          placeholder="请输入您的评论"
          @blur="getSubmitContent"
        ></textarea>
      </div>
      <button class="submit" @click="submitReview()">提交</button>
      <div class="allMessage" style="width:99%" :class="{'show':this.count == 0}">
        <h3>评论列表</h3>
        <div class="messageTow" v-for=" item in this.message " :key="item.id">
          <div>{{item.user_id}}:</div>
          <div class="userContent">{{item.content}}</div>
          <div class="createTime">{{item.create_time}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      detail: {},
      num: 1,
      checked: false,
      remove: false,
      newDetail: {},
      loading: true,
      submitContent: "",
      isshow: false,
      message: [],
      count: 0
    };
  },

  created() {
    this.getData();
    this.getMessage();
  },
  mounted() {
    this.zoomPic();
  },

  methods: {
    getData() {
      this.loading = true;
      this.$axios
        .get("/baseTrade/details", {
          params: {
            id: this.$route.params.id
          }
        })
        .then(res => {
          console.log(res);
          this.detail = res.data;
          this.loading = false;
        });
    },
    addToCart() {
      let newDetail = {
        ...this.detail,
        img: this.detail.bigImage,
        num: this.num,
        checked: this.checked,
        remove: this.remove
      };
      this.newDetail = newDetail;
      if (sessionStorage.getItem("user") == null) {
        this.$message({
          message: "请先登录再购买",
          type: "warning"
        });
      } else {
        this.interface();
      }
    },
    //添加成功
    interface() {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("/order/addGoods", {
          user_phone: userPhone,
          goods_price: this.newDetail.price,
          goods_name: this.newDetail.tradeName,
          goods_img: this.newDetail.img,
          goods_count: this.newDetail.num,
          subtotal: this.newDetail.price * this.newDetail.num
        })
        .then(res => {
          if (res.data.status == 200) {
            this.$message({
              message: "恭喜你，商品已经成功添加",
              type: "success"
            });
            // setTimeout(() => {
            //   this.dialogClose();
            // }, 500);
          }
        });
    },
    //提交评论执行请求方法
    submitReview() {
      //获取sess中个人资料
      let userString = sessionStorage.getItem("user");
      //判断是否为登录状态
      if (userString == null) {
        this.tipsOn();
      } else {
        this.user_id = JSON.parse(userString).phone;
        //在登陆状态下提交评论时判断内容是否为空
        if (this.submitContent == "") {
          this.isNull();
        } else {
          //在登录状态下提交评论不为空时允许提交到后台服务器进行审核
          this.$axios
            .post("/leaveMessage/addLeaveMessage/", {
              content: this.submitContent,
              user_id: this.user_id,
              recipient_id: this.$route.params.id
            })
            .then(res => {
              //判断提交是否成功
              if (res.data.msg === "留言出错") {
                this.defeated();
              } else {
                //提交成功后的提示窗口
                this.success();
                //清空评论输入框
                this.$refs.submitContent.value = "";
              }
            });
        }
      }
    },
    //获取评论框文本的方法
    getSubmitContent() {
      this.submitContent = this.$refs.submitContent.value;
      console.log(this.submitContent);
    },
    //提交评论失败后提示框显示方法
    defeated() {
      this.$alert("提交失败", "评论提交", {
        dangerouslyUseHTMLString: true
      });
    },
    //提交评论成功后提示框显示方法
    success() {
      this.$alert("提交成功等待审核", "评论提交", {
        dangerouslyUseHTMLString: true
      });
    },
    //请登陆后在评论
    tipsOn() {
      this.$alert("请登录您的账号再提交", "温馨提示", {
        dangerouslyUseHTMLString: true
      });
    },
    //提交评论为空
    isNull() {
      this.$alert("请填写内容后提交", "温馨提示", {
        dangerouslyUseHTMLString: true
      });
    },
    getMessage() {
      this.$axios
        .get("/leaveMessage/queryLeaveMessageList", {
          params: {
            page: 1,
            limit: 15,
            recipient_id: this.$route.params.id
          }
        })
        .then(res => {
          console.log(res);
          this.message = res.data.data;
          this.count = res.data.count;
        });
    }
  },

  watch: {
    $route: {
      handler(n) {
        this.getData();
      },
      deep: true,
      immedial: true
    }
  }
};
</script>

<style lang="scss" scoped>
.wrap-materialdetail {
  width: 1472px;
  height: 960px;
  .materialdetail {
    width: 96%;
    height: 530px;
    margin-top: 75px;
    margin-left: 25px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    .left-smallImg {
      width: 120px;
      height: 400px;
      display: flex;
      flex-direction: column;
      justify-content: space-between;
      img {
        width: 120px;
        height: 120px;
        cursor: pointer;
      }
    }
    .zoom {
      width: 830px;
      height: 400px;
      .smallImg {
        width: 400px;
        height: 400px;
        float: left;
        /* 定位span元素 也即拖拽元素的父元素 */
        position: relative;
        img {
          width: 100%;
          height: 100%;
        }
        .zoomspan {
          position: absolute;
          width: 95px;
          height: 95px;
          top: 0;
          left: 0;
          background: rgba(200, 200, 200, 0.7);
          cursor: move;
          display: none;
        }
        .decs {
          width: 373px;
          height: 400px;
          display: flex;
          flex-direction: column;
          justify-content: space-around;
          padding-left: 5px;
          position: absolute;
          top: -9px;
          right: -444px;
          h1 {
            font-size: 30px;
          }
          .text {
            font-size: 20px;
            height: 4opx;
            line-height: 40px;
            letter-spacing: 2px;
          }
          .price {
            font-size: 28px;
            font-weight: 600;
            color: #bf0000;
          }
          button {
            width: 123px;
            height: 54px;
            line-height: 54px;
            background-color: #bf0000;
            border-radius: 40px;
            text-align: center;
            color: #ffffff;
            font-weight: 600;
            font-size: 12px;
            margin-right: 37px;
            outline: none;
            cursor: pointer;
          }
          button:nth-child(2) {
            background-color: green;
          }
        }
      }
      .bigImg {
        width: 400px;
        height: 400px;
        float: right;
        overflow: hidden;
        display: none;
        /* 拖拽元素的父元素 */
        position: relative;
        z-index: 1000;
        img {
          width: 200%;
          height: 200%;
          position: absolute;
        }
      }
    }
  }
  .comment {
    width: 50px;
    height: 46px;
    margin-bottom: 20px;
    button {
      width: 83px;
      height: 46px;
      background-color: black;
      color: #fff;
      font-size: 12px;
      font-weight: 600;
      outline: none;
    }
  }
  .message {
    width: 100%;
    height: 200px;
    border-top: 1px solid #cccccc;
    .textarea {
      margin-top: 35px;
      resize: none;
      outline: none;
      p {
        width: 100%;
        height: 34px;
        line-height: 34px;
        span {
          font-size: 16px;
          margin-right: 50px;
        }
      }
      textarea {
        width: 99%;
        height: 120px;
        margin-top: 5px;
        resize: none;
        outline: none;
        padding: 10px;
      }
    }
    .submit {
      height: 50px;
      width: 14.5%;
      border-radius: 30px;
      outline: none;
      margin-top: 50px;
      margin-left: 85%;
      margin-bottom: 30px;
      background-color: #a30014;
      color: #eee;
      cursor: pointer;
    }
    .show {
      display: none;
    }
    .allMessage {
      width: 70%;
      margin-bottom: 30px;
      h3 {
        padding-bottom: 30px;
      }
      .messageTow {
        padding: 15px 15px 15px 15px;
        border: 1px solid #7f7f7f;
        vertical-align: top;
        .userContent {
          display: block;
          font-size: 14px;
          line-height: 20px;
          padding-top: 8px;
          text-indent: 2em;
        }
        .createTime {
          text-align: right;
          padding-top: 15px;
          font-size: 12px;
          padding-right: 10px;
        }
      }
    }
  }
}
</style>