<template>
  <div class="header content">
    <div class="cart" @click="tips">
      <span class="el-icon-shopping-cart-2">购物车</span>
    </div>
    <div class="header-passport" v-if="!show">
      <a href="#" class="login" @click="flag = true">
        <span>登录</span>
      </a>
      <i class="line">|</i>
      <a href="#" class="resgister" @click="dialogFormVisible = true">
        <span>注册</span>
      </a>
    </div>
    <div class="user" v-if="show">
      <i class="el-icon-user-solid" @click="userInfo"></i>
      <i class="phone" @click="userInfo">{{user}}</i>
      <span @click="exit">退出</span>
    </div>
    <el-dialog :visible.sync="dialogFormVisible" width="34%" @close="handleRegisterClose">
      <Register @closeDialog="handleRegister" ref="child"></Register>
    </el-dialog>
    <el-dialog :visible.sync="flag" width="34%" @close="handleLoginClose">
      <Login @closeDialog="handleClose" @reg="register" @getUser="getUserInfo" ref="child"></Login>
    </el-dialog>
  </div>
</template>

<script>
import Register from "@/components/Register";
import Login from "@/components/Login";

export default {
  name: "AppHeader",
  components: {
    Register,
    Login
  },
  data() {
    return {
      input: "",
      dialogFormVisible: false,
      flag: false,
      user: "",
      show: false
    };
  },
  created() {
    this.getUserInfo();
  },
  methods: {
    handleClose(val) {
      this.flag = val;
    },
    handleRegister(val) {
      this.dialogFormVisible = val;
      setTimeout(() => {
        this.flag = !val;
      }, 1000);
    },
    handleLoginClose() {
      this.$refs.child.resetForm();
    },
    handleRegisterClose() {
      this.$refs.child.resetForm();
    },
    register(val) {
      this.flag = val;
      setTimeout(() => {
        this.dialogFormVisible = !val;
      }, 1000);
    },
    //显示当前用户 该函数由子组件触发执行
    getUserInfo() {
      let userString = sessionStorage.getItem("user");
      if (userString !== null) {
        //修改数据
        this.user = JSON.parse(userString).phone;
        this.$nextTick(() => {
          this.show = true;
        });
      }
    },
    //点击图标进入个人中心
    userInfo() {
      this.$router.push("/PersonalDate");
    },
    //退出登录
    exit() {
      setTimeout(() => {
        this.$router.push("/home");
        sessionStorage.removeItem("user");
        this.show = false;
      }, 500);
    },
    //点击购物车的提示
    tips() {
      let userString = sessionStorage.getItem("user");
      if (userString !== null) {
        this.$router.push("/MineShopcar");
      } else {
        this.$message({
          message: "请登录后再查看购物车",
          type: "warning"
        });
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.header {
  display: flex;
  justify-content: flex-end;
  margin-bottom: 8px;
  margin-top: 12px;
  .sreach-input {
    width: 200px;
    margin-right: 5px;
  }
  .cart a,
  .header-passport,
  .header-passport span {
    line-height: 40px;
    background: #bf0000;
    color: #fff;
    font-size: 12px;
    padding: 0 8px;
    border-radius: 5px;
  }
  .user {
    font-family: Arial, Helvetica, sans-serif;
    line-height: 40px;
    background: #bf0000;
    border-radius: 5px;
    color: #fff;
    font-size: 14px;
    cursor: pointer;
    .el-icon-user-solid {
      font-size: 16px;
      padding-left: 8px;
    }
    .phone {
      padding-right: 8px;
      padding-left: -4px;
    }
    span {
      cursor: pointer;
      color: #fff;
      border-left: 1px solid #ffffff;
      padding: 0 8px;
    }
  }
  .cart {
    margin-right: 5px;
    line-height: 40px;
    background: #bf0000;
    color: #fff;
    font-size: 12px;
    padding: 0 8px;
    border-radius: 5px;
    cursor: pointer;
  }
}
</style>
