<template>
  <div class="teachingModel" v-loading="loading">
    <div class="row">
      <div class="col" v-for="item in list" :key="item.id">
        <router-link tag="li" :to="{name: ''}" class="img">
          <img :src="item.cover" class="cover" @click="dialog">
          <!-- <el-button type="text" class="btn" @click="dialog"></el-button> -->
          <el-dialog :title="item.title" :visible.sync="dialogVisible" width="30%">
            <iframe
              class="video"
              :src="item.videoUrl"
              sandbox="allow-same-origin allow-scripts"
              v-if="flag"
            ></iframe>
          </el-dialog>
        </router-link>
        <p class="detail">{{item.introduction}}</p>
        <el-tooltip :disabled="disabled" :content="item.content" placement="bottom">
          <li class="title" @click="disabled = !disabled">{{item.content}}</li>
        </el-tooltip>
      </div>
    </div>
    <el-pagination
      background=""
      layout="prev, pager, next"
      :pager-count="5"
      :total="count"
      :page-size="3"
      @current-change="handleCurrentChange"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "Teaching",
  data() {
    return {
      list: [],
      dialogVisible: false,
      loading: true,
      currentPage: 1,
      count: 0,
      pageSize: 3,
      flag: true,
      disabled: false,
      content: ""
    };
  },
  created() {
    this.getData();
  },
  methods: {
    dialog() {
      this.dialogVisible = true;
    },
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.getData();
    },
    getData() {
      this.loading = true;
      this.$axios
        .get("/menu/queryTeachVideoList", {
          params: {
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          this.count = res.data.count;
          if (res.data.count > 0) {
            this.list = res.data.data;
            this.loading = false;
          }
        });
    }
  },
  watch: {
    dialogVisible: function(val, f) {
      console.log(val, f);
      if (val == false) {
        window.location.reload();
      }
    }
  }
};
</script>

<style lang="scss">
.teachingModel {
  margin-top: 36px;
  width: 100%;
  .row {
    min-width: 99%;
    min-height: 238px;
    margin-bottom: 20px;
    margin-top: 40px;
    margin-left: 10px;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    position: relative;
    .col {
      width: 30%;
      height: 238px;
      margin-bottom: 100px;
      .img {
        display: inline-block;
        height: 100%;
        width: 100%;
        position: relative;
        // .btn {
        //   width: 100%;
        //   height: 100%;
        //   position: absolute;
        //   top: 0;
        //   left: 0;
        // }
        .cover {
          height: 100%;
          width: 100%;
          transition: all 0.4s linear;
          &:hover {
            transform: scale(0.95) !important;
            box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.5);
          }
        }
        /deep/ .el-dialog {
          background-color: #fff;
          width: 56% !important;
          height: 600px;
          .el-dialog__body {
            height: 100%;
            padding: 0px;
            iframe {
              width: 100%;
              height: 80%;
            }
          }
        }
        // span {
        //   display: inline-block;
        //   width: 48px;
        //   height: 48px;
        //   border-radius: 50%;
        //   background-color: black;
        //   opacity: 0.7;
        //   text-align: center;
        //   line-height: 48px;
        //   position: absolute;
        //   top: 0;
        //   bottom: 0;
        //   left: 0;
        //   right: 0;
        //   margin: auto;
        // }
        .el-icon-caret-right:before {
          font-size: 30px;
          color: #fff;
        }
      }
      .detail {
        height: 24px;
        display: inline-block;
        color: #262626;
        font-size: 18px;
        line-height: 24px;
        margin-bottom: 4px;
        margin-top: 10px;
        max-height: 48px;
        text-overflow: ellipsis;
        overflow: hidden;
        cursor: pointer;
      }
      .title {
        width: 100%;
        height: 24px;
        color: #787878;
        font-size: 13px;
        line-height: 24px;
        list-style: none;
        text-overflow: ellipsis;
        overflow: hidden;
        cursor: pointer;
      }
    }
    .video {
      outline: none;
    }
  }
  .el-pagination {
    text-align: center;
    margin-top: 50px;
  }
  /deep/.el-pagination.is-background .el-pager li:not(.disabled).active {
    background-color: #bf0000;
  }
}
body {
  /deep/.el-tooltip__popper.is-dark {
    width: 306px;
    background: #EBEEF5;
    color: #909399;
  }
  /deep/.el-tooltip__popper[x-placement^="bottom"] .popper__arrow {
    border-bottom-color: #EBEEF5;
  }
  /deep/.el-tooltip__popper[x-placement^="bottom"] .popper__arrow:after {
    border-bottom-color: #EBEEF5;
  }
}
</style>
