<template>
  <div class="propaganda" v-loading="loading">
    <el-tabs v-model="activeName" @tab-click="handleClick(activeName)">
      <el-tab-pane label="说明" name="说明" class="iframe">
        <iframe height="498px" width="95%" src="http://player.youku.com/embed/XNDM1MjIyNDEwOA=="></iframe>
      </el-tab-pane>
      <el-tab-pane label="菜单" name="菜单" class="poster">
        <div class="first">
          <div class="two" v-for="item in list" :key="item.id">
            <img
              :src="item.image"
              alt=""
              width="100%"
              height="100%"
              @click="go(item.id,item.templateType)"
            >
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="传单" name="传单" class="poster">
        <div class="first">
          <div class="two" v-for="item in list" :key="item.id">
            <img
              :src="item.image"
              alt=""
              width="100%"
              height="100%"
              @click="go(item.id,item.templateType)"
            >
          </div>
        </div>
      </el-tab-pane>
      <el-tab-pane label="海报" name="海报" class="poster">
        <div class="first">
          <div class="two" v-for="item in list" :key="item.id">
            <img
              :src="item.image"
              alt=""
              width="100%"
              height="100%"
              @click="go(item.id,item.templateType)"
            >
          </div>
        </div>
      </el-tab-pane>
    </el-tabs>
    <el-pagination
      background=""
      layout="prev, pager, next"
      :pager-count="5"
      :total="count"
      :page-size="6"
      @current-change="handleCurrentChange"
      v-show="flag"
    ></el-pagination>
  </div>
</template>

<script>
export default {
  name: "Propaganda",
  data() {
    return {
      activeName: "说明",
      list: [],
      loading: true,
      currentPage: 1,
      pageSize: 6,
      flag: false,
      count: 0
    };
  },
  created() {
    this.getData();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    //切换菜单时获取数据
    handleClick(activeName) {
      this.activeName = activeName;
      this.getData();
    },
    //获取当前页数
    handleCurrentChange(currentPage) {
      this.currentPage = currentPage;
      this.getData();
    },
    //获取初始数据
    getData() {
      this.loading = true;
      this.$axios
        .get("/template/list", {
          params: {
            templateType: this.activeName,
            page: this.currentPage,
            limit: this.pageSize
          }
        })
        .then(res => {
          console.log(res);
          this.count = res.data.count;
          if (this.activeName !== "说明" && res.data.count > 0) {
            this.list = res.data.data;
            this.flag = true;
          }
          this.loading = false;
        });
    },
    //传参数进入详情页面
    go(id, type) {
      this.$router.push(`/propaganda/menudetail/${id}?type=${type}`);
    }
  },
  watch: {
    $route: {
      handler(n) {
        this.type = n.query.type;
        console.log(this.type);
        this.activeName = this.type;
        this.getData(this.activeName);
        //如果路由没有type参数
        if (!this.type) {
          this.activeName = "说明";
        }
      },
      deep: true,
      immedial: true
    }
  }
};
</script>

<style lang="scss" scoped>
.bigImg {
  padding: 20px;
  border: 1px solid #d3dce6;
  box-shadow: 0 0 13px rgba(0, 0, 0, 0.1);
  transition: all 0.4s;
  &:hover {
    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.5);
    transform: translate(0px, -3px);
  }
}
.first .two {
  width: 320px;
  height: 295px;
  margin: 15px;
  img {
    width: 97%;
    height: 88%;
    cursor: pointer;
    box-shadow: 0px 5px 8px rgba(0, 0, 0, 1.5);
    &:hover {
      box-shadow: 0 5px 8px rgba(0, 0, 0, 0.5);
      transform: translate(0px, -3px);
      transition: all 0.4s;
    }
  }
  iframe {
    padding: 20px;
    border: 1px solid #d3dce6;
  }
}
.first div {
  display: inline-block;
  height: 200px;
  width: 300px;
  overflow: hidden;
}
.poster .first {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: start;
  flex-wrap: wrap;
}
.propaganda {
  width: 95%;
  min-height: 500px;
  margin-top: 34px;
  position: relative;
  .el-tabs {
    width: 105%;
  }
}
.imgs img {
  margin-right: 15px;
  margin-bottom: 15px;
}
.el-row {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0;
  }
}
.el-col {
  border-radius: 4px;
}
.bg-purple-dark {
  background: #99a9bf;
}
.bg-purple {
  background: #d3dce6;
}
.bg-purple-light {
  background: #e5e9f2;
}
.grid-content {
  border-radius: 4px;
  min-height: 36px;
}
.row-bg {
  padding: 10px 0;
  background-color: #f9fafc;
}
.iframe {
  cursor: pointer;
}
.el-pagination {
  position: absolute;
  bottom: -43px;
  left: 429px;
}
/deep/.el-pagination.is-background .el-pager li:not(.disabled).active {
  background-color: #bf0000 !important;
}
</style>
