<template>
  <div class="minemessage" v-loading="loading">
    <div class="title" v-show="list.length">
      <h1>我的川料</h1>
    </div>
    <el-table
      :data="list"
      border=""
      max-height="600"
      style="width: 100%;"
      v-show="list.length"
      highlight-current-row
      ref="multipleTable"
      @select="select"
      @select-all="selectAll"
      :header-cell-style="{
    'background-color': '#fafafa',
    'color': 'rgb(103, 194, 58)',
   
}"
    >
      <el-table-column label="复选框" width="70" type="selection" align="center"></el-table-column>
      <el-table-column prop="img" label="商品图片" width="180" align="center">
        <template slot-scope="scope">
          <img :src="scope.row.goods_img" width="60%" height="70">
        </template>
      </el-table-column>
      <el-table-column prop="goods_name" label="商品名称" width="180"></el-table-column>
      <el-table-column prop="goods_price" label="商品价钱" width="180" align="center"></el-table-column>
      <el-table-column label="商品数量" width="240">
        <template scope="scope" ref="number">
          <el-input-number
            v-model="scope.row.goods_count"
            @change="upData(scope.row.id,scope.row.goods_count,scope.row.goods_price)"
            controls-position="right"
            :min="1"
            :max="scope.row.number"
          ></el-input-number>
        </template>
      </el-table-column>
      <el-table-column label="商品总价" align="center">
        <template scope="scope">
          <div>{{scope.row.goods_price*scope.row.goods_count}}</div>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center" width="100">
        <template scope="scope">
          <el-button slot="reference" type="danger" @click="remove(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="footer" v-show="list.length !==0">
      <div class="count">
        合计：￥
        <span>{{countList}}</span>
      </div>
      <el-row>
        <el-button class="payment" @click="payment()" type="success" plain>结算</el-button>
      </el-row>
    </div>
    <div v-show="list.length==0" class="tips" style="display:none">请添加商品~</div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      list: [],
      count: 0,
      selected: [],
      loading: true,
      contentflag: false
    };
  },
  computed: {
    //计算总价
    countList: function() {
      var a = 0;
      for (let i = 0; i < this.selected.length; i++) {
        a += this.selected[i].goods_price * this.selected[i].goods_count;
      }
      this.count = a;
      localStorage.setItem("count", JSON.stringify(this.count));
      return this.count;
    }
  },
  created() {
    this.find();
  },
  deactivated() {
    this.$destroy();
  },
  methods: {
    //第一个参数返回这个选中的数组 第二个参数返回当前项
    select(selection, row) {
      this.selected = selection;
      console.log(this.selected);
    },
    //返回被选中的所有项 不选中则为空
    selectAll(selection) {
      this.selected = selection;
    },
    //查询购物车
    find() {
      this.loading = true;
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .get("/order/queryGoods", {
          params: {
            user_phone: userPhone
          }
        })
        .then(res => {
          if (res.data.status == 200) {
            this.list = res.data.data;
          }
          this.loading = false;
        });
    },
    //修改购物车
    upData(id, num, price) {
      let userPhone = JSON.parse(sessionStorage.getItem("user")).phone;
      this.$axios
        .post("order/modifyGoods", {
          id: id,
          goods_count: num,
          subtotal: num * price
        })
        .then(res => {
          if (res.data.status == 200) {
            this.list = res.data.data;
          }
        });
    },
    //删除购物车
    remove(id) {
      this.$axios
        .post("order/removeGoodsById", {
          id: id
        })
        .then(res => {
          if (res.data.status == 200) {
            this.find();
          }
        });
    },
    payment() {
      sessionStorage.setItem("shops", JSON.stringify(this.selected));
      this.$router.push({
        path: "/payment"
      });
    }
  },
  watch: {
    //   //监听路由的变化 拿到路由参数最新的值进行赋值 组件没有卸载的状态下
    $route: {
      handler() {
        this.find();
      },
      deep: true,
      immedial: true
    }
  }
};
</script>
<style lang="scss" scoped>
.minemessage {
  .title {
    width: 100%;
    text-align: center;
    font-weight: 700;
    font-size: 40px;
  }
  .el-table {
    margin-top: 46px;
  }
  .footer {
    width: 20%;
    height: 180px;
    margin-top: 34px;
    float: right;
    display: flex;
    flex-direction: column;
    .count {
      width: 100%;
      height: 45px;
      line-height: 45px;
      font-size: 16px;
      text-align: left;
      span {
        font-size: 26px;
        color: #bf0000;
        font-weight: 600;
      }
    }
    .payment {
      width: 124px;
      height: 45px;
      font-weight: 600;
      font-size: 16px;
      letter-spacing: 6px;
      text-align: center;
    }
  }
}
.el-button--default {
  &:hover {
    outline: none;
    background-color: #fff;
  }
}
.tips {
  width: 100%;
  height: 200px;
  padding: 85px 45px;
  font-size: 20px;
  color: #7e7e7e;
  font-family: "微软雅黑";
  letter-spacing: 1px;
}
</style>